import { useQuery } from '@apollo/client';
import { Container, Grid } from '@mui/material';
import React from 'react';
import { useParams } from 'react-router-dom';
import { GET_PUNK_QUERY } from '../../../../queries';
import { GetPunk, GetPunkVariables } from '../../../../queries/__generated__/GetPunk';
import { FormatUtil } from '../../../../util/format.util';
import { Alert } from '../../../common/components/alert';
import { PunkActivity } from '../activity';
import { PunkImage } from '../image';
import { PunkInfo } from '../info';

export const PunkDetails: React.FC = (props: any) => {
  const { id } = useParams();

  const query = useQuery<GetPunk, GetPunkVariables>(GET_PUNK_QUERY, {
    variables: {
      id: id ?? "-1"
    },
    fetchPolicy: 'network-only'
  });

  const error = query.error;
  const loading = query.loading;

  if (error) {
    return <Alert message={FormatUtil.formatMessage(error)} type="warning"></Alert>
  }

  return (
    <>
      {loading || !query.data ? (
        <Alert message="Loading..." type="default" ></Alert>
      ) : (
        <Container maxWidth="lg">
          <Grid container spacing={3} direction="row" marginBottom={3}>
            <Grid item xs={12} md={5}>
              <PunkImage image={query.data.punk.image}></PunkImage>
            </Grid>
            <Grid item xs={12} md={7}>
              <PunkInfo item={query.data.punk}></PunkInfo>
            </Grid>
            <Grid item xs={12}>
              <PunkActivity item={query.data.punk}></PunkActivity>
            </Grid>
          </Grid>
        </Container>
      )}
    </>
  )
}
