import fs from 'fs';

export abstract class FileUtil {
  abstract readFileAsync(path: string, encoding?: BufferEncoding): Promise<any>;
}

export class BrowserFileUtil extends FileUtil {
  async readFileAsync(path: string, encoding: BufferEncoding = 'utf8'): Promise<any> {
    const response = await fetch(path);
    switch (encoding) {
      case 'binary':
        return response.arrayBuffer();
      default:
        return response.text();
    }

  }
}

export class LocalFileUtil extends FileUtil {
  public async readFileAsync(path: string, encoding: BufferEncoding = 'utf8'): Promise<any> {
    return Promise.resolve(fs.readFileSync(path, encoding));
  }
}

export enum FileUtilType {
  Browser = 0,
  Local = 1
}

export const getFileUtil = (type: FileUtilType = FileUtilType.Browser) => {
  switch (type) {
    case FileUtilType.Local:
      return new LocalFileUtil();
    default:
      return new BrowserFileUtil();
  }
}