import { FetchPolicy } from '@apollo/client';
import { Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, FormControl, Input, InputAdornment, InputLabel } from '@mui/material';
import { BigNumber } from 'bignumber.js';
import React, { useEffect, useState } from 'react';
import { CommonConstants } from '../../../../common/constants';
import { getMarketService } from '../../../../services/market.service';
import { CommonUtil } from '../../../../util/common.util';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { TokenUtil } from '../../../../util/token.util';
import { Punk, PunkDialogState } from '../../../../util/types';
import { ClickOnceButton } from '../../../common/components/click-once-button';

interface Props {
  item: Punk
  state: PunkDialogState
  fee: BigNumber
  updateFee: (_fetchPolicy?: FetchPolicy) => Promise<void>
  setState: React.Dispatch<React.SetStateAction<PunkDialogState>>
  refetchAsync: () => Promise<void>
}

export const PunkOfferDialog: React.FC<Props> = (props: Props) => {
  const [amount, setAmount] = useState<string>("");
  const [address, setAddress] = useState<string>("");
  const [disabled, setDisabled] = useState<boolean>(false);
  const service = getMarketService();

  const changeAmount = (amount: string) => {
    setAmount(amount);
    setDisabled(CommonUtil.isNullOrWhitespace(amount) || new BigNumber(amount).lt(0));
  }

  const updateFee = props.updateFee;

  useEffect(() => {
    if (props.state.open) {
      changeAmount("");
      setAddress("");
      updateFee("cache-first");
    }
  }, [props.state, updateFee]);

  const handleClose = () => {
    props.setState({
      ...props.state,
      open: false
    })
  };

  const handleAmountChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    changeAmount(event.target.value);
  };

  const handleAddressChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setAddress(event.target.value);
  };

  const handleConfirmAsync = async () => {
    try {
      const _amount = new BigNumber(amount).shiftedBy(CommonConstants.CURRENCY_DECIMALS);
      const _success = await service.offerAsync(props.item.id, _amount.toFixed(), CommonUtil.isNullOrWhitespace(address) ? undefined : address);
      if (_success) {
        SnackbarUtil.enqueueMessage("Listed for sale");
        handleClose();
      } else {
        SnackbarUtil.enqueueError("Error");
        updateFee();
      }
    } catch (error) {
      SnackbarUtil.enqueueError(error);
    } finally {
      await props.refetchAsync();
    }
  }

  return (
    <Dialog onClose={handleClose} open={props.state.open} maxWidth="xs" fullWidth>
      <DialogTitle>Sell</DialogTitle>
      <DialogContent>
        <DialogContentText>
          List {CommonConstants.PUNK_NAME} #{props.item.id} for sale
        </DialogContentText>
        <FormControl fullWidth sx={{ mt: 2 }} variant="standard" >
          <InputLabel htmlFor="amount">Amount</InputLabel>
          <Input
            id="amount"
            value={amount}
            onChange={handleAmountChange}
            type="number"
            autoFocus
            startAdornment={<InputAdornment position="start">${CommonConstants.CURRENCY_NAME}</InputAdornment>}
          />
        </FormControl>
        {props.fee.gt(0) ? (
          <DialogContentText align='right'>
            + {TokenUtil.formatVite(props.fee)} {CommonConstants.CURRENCY_NAME} protocol fee
          </DialogContentText>
        ) : (
          <Box marginTop={2}></Box>
        )}
        <FormControl fullWidth variant="standard" >
          <InputLabel htmlFor="address">Address (optional)</InputLabel>
          <Input
            id="address"
            value={address}
            onChange={handleAddressChange}
            placeholder="Only this address will be eligible to buy"
            type="text"
          />
        </FormControl>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>Cancel</Button>
        <ClickOnceButton size="medium" color="primary" callbackFn={handleConfirmAsync} disabled={disabled}>
          Confirm
        </ClickOnceButton>
      </DialogActions>
    </Dialog>
  )
}
