import { CommonConfig } from "../common/config";
import { CommonUtil } from "./common.util";

export abstract class ExplorerUtil {
  private static DEFAULT_BASE_URL = "https://vitescan.io";

  private static getBaseUrl(_baseUrl?: string): string {
    let baseUrl = _baseUrl;
    if (!baseUrl || CommonUtil.isNullOrWhitespace(baseUrl)) {
      baseUrl = CommonConfig.EXPLORER_URL;
    }
    baseUrl = baseUrl.endsWith('/') ? baseUrl.substring(0, baseUrl.length - 1) : baseUrl;
    return baseUrl;
  }

  static getAddressUrl(_address?: Maybe<string>, _baseUrl?: string): string {
    const baseUrl = ExplorerUtil.getBaseUrl(_baseUrl);
    if (baseUrl === ExplorerUtil.DEFAULT_BASE_URL) {
      return `${baseUrl}/address/${_address}`;
    }
    return `${baseUrl}/account/${_address}`;
  }

  static getTxUrl(_tx?: Maybe<string>, _baseUrl?: string): string {
    const baseUrl = ExplorerUtil.getBaseUrl(_baseUrl);
    if (baseUrl === ExplorerUtil.DEFAULT_BASE_URL) {
      return `${baseUrl}/tx/${_tx}`;
    }
    return `${baseUrl}/tx/${_tx}`;
  }
}