import { styled } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { CommonConstants } from '../../../../common/constants';
import { getPunkService } from '../../../../services/punk.service';
import { UrlUtil } from '../../../../util/url.util';

const ImageContainer = styled('div')(
  ({ theme }) => `
  position: relative;
  width: 80vw;
  height: 80vw; 
  max-width: 400px;
  max-height: 400px;
  animation: lightning 4s ease-out infinite;
`,
);

const Image = styled('div')(
  ({ theme }) => `
  width: 100%;
  height: 100%;
  border: 5px solid #fff;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url('${CommonConstants.ITEM_BG_URL}');
`,
);

const Img1 = styled('div')(
  ({ theme }) => `
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url('${CommonConstants.SAMPLE_PUNK_URL}');
  border-radius: 50%;
  animation: toggle 4s linear -2s infinite alternate;
  image-rendering: pixelated;
`,
);


const Img2 = styled('img')(
  ({ theme }) => `
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: 5px solid #fff;
  border-radius: 50%;
  visibility: hidden;
  animation: toggle 4s linear 2s infinite alternate;
`,
);

interface Props {
  punkId1?: Maybe<number>
  punkId2?: Maybe<number>
}


export const LandingMainImage: React.FC<Props> = (props: Props) => {
  const [imageUrl1, setImageUrl1] = useState<Maybe<string>>(undefined);
  const [imageUrl2, setImageUrl2] = useState<Maybe<string>>(undefined);
  const service = getPunkService();

  useEffect(() => {
    const setImageUrlsAsync = async () => {
      if (props.punkId1) {
        const punk1 = await service.getById(props.punkId1.toString());
        setImageUrl1(UrlUtil.getPunkUrl(punk1.data.punk.image));
      }
      if (props.punkId2) {
        const punk2 = await service.getById(props.punkId2.toString());
        setImageUrl2(UrlUtil.getPunkUrl(punk2.data.punk.image));
      }
    }
    setImageUrlsAsync();
  }, [props.punkId1, props.punkId2, service])

  return (
    <ImageContainer>
      <Image>
        <Img1 sx={{ backgroundImage: `url('${imageUrl1 ?? CommonConstants.SAMPLE_PUNK_URL}')` }} />
        <Img2 sx={{ backgroundImage: `url('${imageUrl2 ?? CommonConstants.SAMPLE_PUNK_URL}')` }} />
      </Image>
    </ImageContainer>
  );
}
