import { useQuery } from '@apollo/client';
import { faGem } from '@fortawesome/free-regular-svg-icons';
import { Verified } from '@mui/icons-material';
import { Box, Card, Chip, Skeleton, Stack, styled, Typography, useMediaQuery, useTheme } from '@mui/material';
import { BigNumber } from 'bignumber.js';
import React, { useEffect, useState } from 'react';
import { CommonConstants } from '../../../../common/constants';
import { useConnectedWeb3Context } from '../../../../contexts/connectedWeb3';
import { usePunkInfo } from '../../../../hooks/punk.hook';
import { GET_MARKET_INFO_QUERY } from '../../../../queries';
import { GetMarketInfo } from '../../../../queries/__generated__/GetMarketInfo';
import { CommonUtil } from '../../../../util/common.util';
import { getEmitter } from '../../../../util/emitter';
import { FormatUtil } from '../../../../util/format.util';
import { getLogger } from '../../../../util/logger';
import { TokenUtil } from '../../../../util/token.util';
import { DocumentState, GlobalEvent, Punk, PunkChangedEvent } from '../../../../util/types';
import { Alert } from '../../../common/components/alert';
import { ExplorerLink, ExplorerLinkType } from '../../../common/components/explorer-link';
import { FontAwesomeSvgIcon } from '../../../common/components/fa-icon';
import { BootstrapTooltip, ExtendedBootstrapTooltip } from '../../../common/components/tooltip';
import { PunkInfoActions } from '../info-actions';

const logger = getLogger();

const InfoContainer = styled(Card)(
  ({ theme }) => `
  position: relative;
  overflow: auto;
  width: 100%;
  height: 80vw;
  min-height: 320px;
  max-height: 400px;
  background-color: rgba(255, 255, 255, 0.5);
`,
);

const BottomContent = styled(Box)(
  ({ theme }) => `
  position: absolute;
  bottom: 24px;
  left: 24px;
  width: calc(100% - 48px);
`,
);

interface Props {
  item: Punk
}

export const PunkInfo: React.FC<Props> = (props: Props) => {
  const mounted = React.useRef(false);
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('sm'));
  const [marketStarted, setMarketStarted] = useState(false);
  const { punkExtended, loading, error, refetchAsync } = usePunkInfo(props.item);
  const query = useQuery<GetMarketInfo>(GET_MARKET_INFO_QUERY, {
    fetchPolicy: 'network-only'
  });
  const context = useConnectedWeb3Context();
  const emitter = getEmitter();

  const refetch = query.refetch;

  useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  useEffect(() => {
    if (error) {
      logger.error(error)();
    }
  }, [error]);

  useEffect(() => {
    if (mounted.current) {
      const info = query.data?.marketInfo;
      if (info && mounted.current) {
        setMarketStarted(info.marketStarted);
      }
    }
  }, [query.data]);

  useEffect(() => {
    const handleEvent = (state: DocumentState) => {
      if (mounted.current) {
        if (state === DocumentState.Active) {
          refetchAsync();
        }
      }
    }
    emitter.on(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    return () => {
      emitter.off(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    };
  }, [emitter, refetchAsync]);

  useEffect(() => {
    const handleEvent = () => {
      if (mounted.current) {
        refetch();
      }
    }
    emitter.on(GlobalEvent.MarketChanged, handleEvent);
    return () => {
      emitter.off(GlobalEvent.MarketChanged, handleEvent);
    };
  }, [emitter, refetch]);

  useEffect(() => {
    const handleEvent = (e: PunkChangedEvent) => {
      if (mounted.current) {
        if (e.punkId === parseInt(props.item.id)) {
          refetchAsync();
        }
      }
    }
    emitter.on(GlobalEvent.PunkChanged, handleEvent);
    return () => {
      emitter.off(GlobalEvent.PunkChanged, handleEvent);
    };
  }, [emitter, props.item.id, refetchAsync]);

  const formatViteTooltip = (bn: BigNumber) => {
    return TokenUtil.formatViteTooltip(bn);
  }

  const formatVite = (bn: BigNumber) => {
    return bn.gt(0) ? TokenUtil.formatVite(bn) : "0";
  }

  return (
    <InfoContainer>
      <Box sx={{ p: 3 }}>
        <Stack direction="row" justifyContent="space-between">
          <Typography variant="h5" fontSize={matches ? "1.25rem" : "1.5rem"} color="primary" paragraph>{CommonConstants.PUNK_NAME} #{props.item.id}</Typography>
          <Box>
            <ExtendedBootstrapTooltip title={<span>Rarity rank</span>} placement="bottom" arrow>
              <Chip size="small" icon={<FontAwesomeSvgIcon icon={faGem} sx={{ fontSize: "small", ml: 1 }} />} sx={{ fontSize: "0.7rem" }} label={props.item.rank} />
            </ExtendedBootstrapTooltip>
          </Box>
        </Stack>
        {props.item.attributes.length > 0 && (
          <Stack spacing={1} direction="row" sx={{ mb: 2 }}>
            {props.item.attributes?.map((a: string) => (
              <Chip key={a} label={a} variant="outlined" size={matches ? "small" : "medium"} />
            ))}
          </Stack>
        )}
        {error ? (
          <Alert message={FormatUtil.formatMessage(error)} type="warning"></Alert>
        ) : (
          <>
            <Stack spacing={1} direction="row" sx={{ mb: 2 }}>
              <Typography display="inline">
                <b>Owner</b>:
              </Typography>
              {loading ? (
                <Skeleton animation="wave" height={24} width="40%" sx={{ display: "inline" }} />
              ) : (
                <Typography display="inline" component="div">
                  {!punkExtended.owner ? (
                    <>
                      Not minted yet.
                    </>
                  ) : (
                    <>
                      <ExplorerLink type={ExplorerLinkType.Address} value={punkExtended.owner}>
                        <ExtendedBootstrapTooltip title={punkExtended.owner}>
                          <span>{CommonUtil.truncateStringInTheMiddle(punkExtended.owner, 20, 5)}</span>
                        </ExtendedBootstrapTooltip>
                      </ExplorerLink>
                      {context.account && punkExtended.owner.toLowerCase() === context.account.toLowerCase() && (
                        <Chip size="small" icon={<Verified />} label="You own this punk" variant="filled" sx={{ borderRadius: 1, ml: 1 }} color="secondary" />
                      )}
                    </>
                  )}
                </Typography>
              )}
            </Stack>
            <Stack spacing={1} direction="row" sx={{ mb: 2 }}>
              <Typography display="inline">
                <b>Current bid</b>:
              </Typography>
              {loading ? (
                <Skeleton animation="wave" height={24} width="60%" sx={{ display: "inline" }} />
              ) : (
                <Typography display="inline">
                  {!punkExtended.bid ? (
                    <>
                      -
                    </>
                  ) : (
                    <>
                      <BootstrapTooltip title={formatViteTooltip(punkExtended.bid.amount)}>
                        <span>{formatVite(punkExtended.bid.amount)} {CommonConstants.CURRENCY_NAME}</span>
                      </BootstrapTooltip>
                      &nbsp;by <ExplorerLink type={ExplorerLinkType.Address} value={punkExtended.bid.bidder}><ExtendedBootstrapTooltip title={punkExtended.bid.bidder}>
                        <span>{CommonUtil.truncateStringInTheMiddle(punkExtended.bid.bidder, 10, 5)}</span>
                      </ExtendedBootstrapTooltip></ExplorerLink>
                    </>
                  )}
                </Typography>
              )}
            </Stack>
            <Stack spacing={1} direction="row" sx={{ mb: 2 }}>
              {(loading || punkExtended.offer) ? (
                <>
                  <Typography display="inline">
                    {punkExtended.offer?.minAmount.lte(0) ? (
                      <>
                        <b>Gift</b>:
                      </>
                    ) : (
                      <>
                        <b>Buy price</b>:
                      </>
                    )}
                  </Typography>
                  {loading ? (
                    <Skeleton animation="wave" height={24} width="30%" sx={{ display: "inline" }} />
                  ) : (
                    <Typography display="inline">
                      {!punkExtended.offer ? (
                        <>
                        </>
                      ) : (
                        <>
                          <BootstrapTooltip title={formatViteTooltip(punkExtended.offer.minAmount)}>
                            <span>{formatVite(punkExtended.offer.minAmount)} {CommonConstants.CURRENCY_NAME}</span>
                          </BootstrapTooltip>
                          {punkExtended.offer.onlySellTo && !CommonUtil.isNullOrWhitespace(punkExtended.offer.onlySellTo) && (
                            <>
                              &nbsp;(<ExplorerLink type={ExplorerLinkType.Address} value={punkExtended.offer.onlySellTo}><ExtendedBootstrapTooltip title={punkExtended.offer.onlySellTo}>
                                <span>{CommonUtil.truncateStringInTheMiddle(punkExtended.offer.onlySellTo, 10, 5)}</span>
                              </ExtendedBootstrapTooltip></ExplorerLink>)
                            </>
                          )}
                        </>
                      )}
                    </Typography>
                  )}
                </>
              ) : (
                <Chip label="Not for sale" variant="filled" sx={{ borderRadius: 1 }} />
              )}
            </Stack>
          </>
        )}
        <BottomContent>
          <PunkInfoActions marketStarted={marketStarted} item={punkExtended} refetchAsync={refetchAsync}></PunkInfoActions>
        </BottomContent>
      </Box>
    </InfoContainer>
  )
}
