import AccountCircleTwoToneIcon from '@mui/icons-material/AccountCircleTwoTone';
import LogoutIcon from '@mui/icons-material/Logout';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { AppBar, Button, Chip, styled, Toolbar, Typography, useMediaQuery, useTheme } from '@mui/material';
import React from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { CommonConstants } from '../../../../common/constants';
import { useConnectedWeb3Context } from '../../../../contexts/connectedWeb3';
import { CommonUtil } from '../../../../util/common.util';
import { ExtendedBootstrapTooltip } from '../../../common/components/tooltip';
import { LoginDialog } from '../../../main/components/login';

const Root = styled('div')(
  ({ theme }) => `
  flex-grow: 1;
`);

const TitleTypography = styled(Typography)(
  ({ theme }) => `
  flex-grow: 1;
  margin-left: 10px !important;
`);

export const Header: React.FC = (props: any) => {
  const navigate = useNavigate();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.up('sm'));
  const context = useConnectedWeb3Context();
  const [loginOpen, setLoginOpen] = React.useState(false);

  const handleClickLogin = () => {
    setLoginOpen(true);
  };

  const handleClickLogout = () => {
    context.logout();
  };

  const handleLoginClose = () => {
    setLoginOpen(false);
  };

  const truncateAddress = (address?: string) => {
    return CommonUtil.truncateStringInTheMiddle(address, 10, 5)
  }

  const showAccount = () => {
    navigate(`/account/${context.account}`);
  }

  return (
    <Root>
      <AppBar position="static" color="transparent">
        <Toolbar>
          <a href="./" style={{ height: "40px" }}>
            <img src={"./assets/images/logo640.png"} alt="logo" width="40" />
          </a>
          <TitleTypography variant="h6" color="#fff">
            <a href="./" style={{ color: "inherit", textDecoration: "inherit" }}>
              {CommonConstants.APP_NAME}
            </a>
          </TitleTypography>
          <Button sx={{ mr: 2 }} component={RouterLink} to='/marketplace'>
            {matches ? (
              <>
                Marketplace
              </>
            ) : (
              <ShoppingCartIcon />
            )}
          </Button>
          {context.account ? (
            <>
              <ExtendedBootstrapTooltip sx={{ mr: 1 }} title={
                <React.Fragment>
                  <p>{context.account}</p>
                </React.Fragment>
              } placement="bottom" arrow>
                {matches ? (
                  <Chip icon={<AccountCircleTwoToneIcon />} label={truncateAddress(context.account)} variant="outlined" onClick={showAccount} />
                ) : (
                  <Button color="inherit" onClick={showAccount}>
                    <AccountCircleTwoToneIcon />
                  </Button>
                )}
              </ExtendedBootstrapTooltip>
              <Button color="inherit" onClick={handleClickLogout}>
                <LogoutIcon />
              </Button>
            </>
          ) : (
            <Button variant="contained" color="primary" onClick={handleClickLogin}>
              Connect wallet
            </Button>
          )}
          <LoginDialog open={loginOpen} setOpen={setLoginOpen} onClose={handleLoginClose}></LoginDialog>
        </Toolbar>
      </AppBar >
    </Root >
  )
}