import { useSnackbar } from 'notistack';
import React, { useEffect, useState } from 'react';
import { HashRouter, Route, Routes } from 'react-router-dom';
import { CommonConfig } from '../../../../common/config';
import { getCommonContext } from '../../../../contexts/common.context';
import { CommonUtil } from '../../../../util/common.util';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { DocumentState, GlobalEvent } from '../../../../util/types';
import { AccountDetails } from '../../../account/components/details';
import { Test } from '../../../common/components/test';
import { TestImage } from '../../../common/components/test-image';
import { TestList } from '../../../common/components/test-list';
import { LandingMain } from '../../../landing/components/main';
import { Header } from '../../../layout/components/header';
import { MainScroll } from '../../../layout/components/main-scroll';
import { MainWrapper } from '../../../layout/components/main-wrapper';
import { MarketplaceBrowse } from '../../../marketplace/components/browse';
import { MarketplaceItem } from '../../../marketplace/components/item';
import { MintDialog } from '../../../punk/components/dialog-mint';
import { PunkDetails } from '../../../punk/components/main';
import { ConfirmTransactionDialog } from '../dialog-transaction';
import { ContractQuota } from '../quota';

const logger = getLogger();

export const Main: React.FC = (props: any) => {
  const [visibility, setVisibility] = useState<DocumentState>(DocumentState.Unknown);
  const emitter = getEmitter();
  const context = getCommonContext();
  const basename = CommonConfig.BASENAME ?? "";
  SnackbarUtil.snackbar = useSnackbar();

  useEffect(() => {
    const handleEvent = async () => {
      try {
        await context.datasource.initAsync(context.network);
        emitter.emitVisibilityChanged(CommonUtil.getDocumentState());
      } catch (error) {
        console.log(error);
      }
    }
    emitter.on(GlobalEvent.ReInit, handleEvent);
    return () => {
      logger.info("emitter.off(GlobalEvent.ReInit, handleEvent)")();
      emitter.off(GlobalEvent.ReInit, handleEvent);
    };
  }, [emitter, context.datasource, context.network]);

  useEffect(() => {
    const handleEvent = async () => {
      const state = CommonUtil.getDocumentState();
      if (visibility !== state) {
        if (state === DocumentState.Active) {
          // await context.datasource.initAsync(context.network);
        }
        logger.info(`visibilitychange before ${visibility} after ${state}`)();
        setVisibility(state);
        emitter.emitVisibilityChanged(state);
      }
    }
    document.addEventListener('visibilitychange', handleEvent);
    return () => {
      logger.info("removeEventListener visibilitychange")();
      document.removeEventListener('visibilitychange', handleEvent);
    };
  }, [emitter, visibility, context.datasource, context.network]);

  return (
    <MainWrapper>
      <HashRouter basename={basename}>
        <Header />
        <MainScroll>
          <ContractQuota></ContractQuota>
          <Routes>
            <Route path="/account/:address" element={<AccountDetails />} />
            <Route path="/marketplace" element={<MarketplaceBrowse />} />
            <Route path="/marketplace/:tab" element={<MarketplaceBrowse />} />
            <Route path="/marketplace/item/:id" element={<MarketplaceItem />} />
            <Route path="/punk/:id" element={<PunkDetails />} />
            <Route path="/test-image" element={<TestImage />} />
            <Route path="/test-list" element={<TestList />} />
            <Route path="/test" element={<Test />} />
            <Route path="/" element={<LandingMain />} />
          </Routes>
          <MintDialog></MintDialog>
          <ConfirmTransactionDialog></ConfirmTransactionDialog>
        </MainScroll>
      </HashRouter>
    </MainWrapper>
  )
}
