import EventEmitter from 'events';
import { DocumentState, GlobalEvent, MarketChangedEvent, PunkChangedEvent, PunkFilterValues, ViteQuotaResult } from './types';

export interface IGlobalEmitter {
  emitReInit(): void
  emitVisibilityChanged(state: DocumentState): void
  emitConfirmTransactionDialog(open: boolean): void
  emitConnectWalletDialog(open: boolean): void
  emitQuotaChanged(address: string, quota: ViteQuotaResult): void
  emitMarketChanged(event: MarketChangedEvent): void
  emitPunkChanged(event: PunkChangedEvent): void
  emitPunkFilterValuesChanged(oldValues: PunkFilterValues, newValues: PunkFilterValues): void
  on(event: string | symbol, listener: (...args: any[]) => void): this
  off(event: string | symbol, listener: (...args: any[]) => void): this
}

export class GlobalEmitter extends EventEmitter implements IGlobalEmitter {
  emitReInit(): void {
    this.emit(GlobalEvent.ReInit)
  }
  emitVisibilityChanged(state: DocumentState): void {
    this.emit(GlobalEvent.DocumentVisibilityChanged, state)
  }
  emitConfirmTransactionDialog(open: boolean): void {
    this.emit(GlobalEvent.ConfirmTransactionDialog, open)
  }
  emitConnectWalletDialog(open: boolean): void {
    this.emit(GlobalEvent.ConnectWalletDialog, open)
  }
  emitQuotaChanged(address: string, quota: ViteQuotaResult): void {
    this.emit(GlobalEvent.QuotaChanged, address, quota);
  }
  emitMarketChanged(event: MarketChangedEvent): void {
    this.emit(GlobalEvent.MarketChanged, event);
  }
  emitPunkChanged(event: PunkChangedEvent): void {
    this.emit(GlobalEvent.PunkChanged, event);
  }
  emitPunkFilterValuesChanged(oldValues: PunkFilterValues, newValues: PunkFilterValues): void {
    this.emit(GlobalEvent.PunkFilterValuesChanged, oldValues, newValues)
  }
}

const emitter = new GlobalEmitter();

export const getEmitter = (): IGlobalEmitter => {
  return emitter;
}
