import { ApolloError, QueryResult, useQuery } from '@apollo/client';
import { Box, Grid } from '@mui/material';
import React, { useEffect, useState } from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { DefaultPunkFilterValues } from '../../../../common/constants';
import { GET_PUNKS_QUERY } from '../../../../queries';
import { GetPunks, GetPunksVariables, GetPunks_punks } from '../../../../queries/__generated__/GetPunks';
import { getEmitter } from '../../../../util/emitter';
import { FormatUtil } from '../../../../util/format.util';
import { getLogger } from '../../../../util/logger';
import { GlobalEvent, Punk, PunkFilterValues } from '../../../../util/types';
import { OrderBy, PunkSortBy } from '../../../../__generated__/globalTypes';
import { Alert } from '../../../common/components/alert';
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { MarketplaceBrowseItem } from '../browse-item';
import { MarketplaceBrowseItemsFilter } from '../browse-items-filter';

const logger = getLogger()

interface EmptyResultProps {
  error: ApolloError | undefined
  query: QueryResult<GetPunks, GetPunksVariables>
}

const EmptyResult: React.FC<EmptyResultProps> = (props: EmptyResultProps) => {
  return (
    <>
      {props.error ? (
        <Alert message={FormatUtil.formatMessage(props.error)} type="warning"></Alert>
      ) : (
        <>
          {props.query.loading || (props.query.data && props.query.data.punks.length > 0) ? (
            <></>
          ) : (
            <Alert message="No punks found" type="default" ></Alert>
          )}
        </>
      )}
    </>
  )
}

interface MarketplaceLoadingProps {
  loading: boolean
  error: ApolloError | undefined
  query: QueryResult<GetPunks, GetPunksVariables>
}

const MarketplaceLoading: React.FC<MarketplaceLoadingProps> = (props: MarketplaceLoadingProps) => {
  return (
    <Grid container spacing={3} justifyContent="center" alignItems="center">
      <Grid item key='1' xs={12} md={6}>
        {props.loading ? (
          <Alert message="Loading..." type="default" ></Alert>
        ) : (
          <EmptyResult error={props.error} query={props.query}></EmptyResult>
        )}
      </Grid>
    </Grid>
  )
}

export const MarketplaceBrowseItems: React.FC = (props: any) => {
  const limit = 8;
  const [isLoading, setIsLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [error, setError] = useState<any>(undefined);
  const [filter, setFilter] = useState<PunkFilterValues>({
    sortBy: DefaultPunkFilterValues.sortBy,
    orderBy: DefaultPunkFilterValues.orderBy,
    search: ''
  })
  const emitter = getEmitter();

  const query = useQuery<GetPunks, GetPunksVariables>(GET_PUNKS_QUERY, {
    variables: {
      offset: 0,
      limit: limit,
      sort: filter.sortBy as PunkSortBy,
      order: filter.orderBy as OrderBy,
      search: filter.search
    },
    fetchPolicy: 'network-only'
  });

  useEffect(() => {
    setIsLoading(query.loading);
  }, [query.loading]);

  useEffect(() => {
    setError(query.error);
  }, [query.error]);

  const fetchMoreDataAsync = async () => {
    const startTimestamp = new Date().getTime();
    const currentLength = query.data?.punks.length ?? 0;
    setIsLoading(true);
    try {
      const result = await query.fetchMore({
        variables: {
          offset: currentLength,
          limit: limit
        }
      });
      logger.info(`fetchMoreDataAsync1 took ${new Date().getTime() - startTimestamp}ms`)();
      setHasMore(result.data.punks.length >= limit);
    } catch (error) {
      setError(error);
    }
    setIsLoading(false);
    logger.info(`fetchMoreDataAsync2 took ${new Date().getTime() - startTimestamp}ms`)();
  };

  const refetch = query.refetch;

  useEffect(() => {
    refetch({
      offset: 0,
      limit: limit,
      sort: filter.sortBy as PunkSortBy,
      order: filter.orderBy as OrderBy,
      search: filter.search
    })
  }, [filter, refetch])

  useEffect(() => {
    const handleEvent = (oldValues: PunkFilterValues, newValues: PunkFilterValues) => {
      setHasMore(true);
      setFilter({
        sortBy: newValues.sortBy,
        orderBy: newValues.orderBy,
        search: newValues.search
      });
    }
    emitter.on(GlobalEvent.PunkFilterValuesChanged, handleEvent)
    return () => {
      logger.info('emitter.off', GlobalEvent.PunkFilterValuesChanged)()
      emitter.off(GlobalEvent.PunkFilterValuesChanged, handleEvent)
    };
  }, [emitter])

  return (
    <>
      <MarketplaceBrowseItemsFilter></MarketplaceBrowseItemsFilter>
      {isLoading || (query.data && query.data.punks.length > 0) ? (
        <>
          <InfiniteScroll
            dataLength={query.data?.punks.length ?? 0}
            next={fetchMoreDataAsync}
            hasMore={hasMore}
            loader={
              <MarketplaceLoading loading={isLoading} error={error} query={query}></MarketplaceLoading>
            }
            scrollThreshold="200px"
            scrollableTarget="mainscroll"
          >
            <Grid container spacing={3} justifyContent="center" alignItems="center" marginBottom={3}>
              {query.data?.punks.map((p: GetPunks_punks) => (
                <Grid item key={p.id} xs={12} sm={6} md={4} lg={3}>
                  <MarketplaceBrowseItem item={p as Punk}></MarketplaceBrowseItem>
                </Grid>
              ))}
            </Grid>
          </InfiniteScroll>
          {hasMore && !isLoading && (
            <Box sx={{ width: "100%", display: "flex", justifyContent: "center" }}>
              <ClickOnceButton variant="outlined" callbackFn={fetchMoreDataAsync} sx={{ minWidth: "200px" }}>Load more</ClickOnceButton>
            </Box>
          )}
        </>
      ) : (
        <MarketplaceLoading loading={isLoading} error={error} query={query}></MarketplaceLoading>
      )}
    </>
  )
}
