import { Link } from '@mui/material';
import React, { useCallback, useEffect, useState } from 'react';
import { CommonUtil } from '../../../../util/common.util';
import { ExplorerUtil } from '../../../../util/explorer.util';

interface Props {
  children: React.ReactNode
  value?: Maybe<string>
  type: ExplorerLinkType
}

export enum ExplorerLinkType {
  Address,
  Transaction
}

export const ExplorerLink: React.FC<Props> = (props: Props) => {
  const getUrl = useCallback(() => {
    if (!props.value || CommonUtil.isNullOrWhitespace(props.value)) {
      return undefined;
    }
    switch (props.type) {
      case ExplorerLinkType.Address:
        return ExplorerUtil.getAddressUrl(props.value);
      default:
        return ExplorerUtil.getTxUrl(props.value);
    }
  }, [props.value, props.type]);
  const [url, setUrl] = useState<Maybe<string>>(getUrl());

  useEffect(() => {
    setUrl(getUrl());
  }, [props.value, props.type, getUrl]);

  if (!url || CommonUtil.isNullOrWhitespace(url)) {
    return (
      <>
        {props.children}
      </>
    )
  }

  return (
    <Link underline="hover" href={url} target="_blank">
      {props.children}
    </Link>
  );
}
