import BigNumber from "bignumber.js"

export enum DocumentState {
  Unknown,
  Hidden,
  Active
}

export enum WalletType {
  MetaMask = 'MetaMask',
}

export enum OrderType {
  ASC = 0,
  DESC = 1
}

export type Network = {
  id: number
  networkId: number
  name: string
  rpcUrl: string
  connectorUrl: string
}

export interface GenericType {
  name: string
  type: string
}

export type DisplayBigNumber = {
  number: BigNumber
  displayNumber: string
}

export type Contract = {
  address: string
  contractName: string
  binary: string
  offChain: string
  abi: any[]
}

export const TypeNames = {
  Punk: "Punk",
  PunkEvent: "PunkEvent",
  MarketBid: "MarketBid",
  MarketOffer: "MarketOffer"
}

export type Punk = {
  __typename: string
  id: string
  rank: number
  image: string
  attributes: string[]
  owner?: Maybe<string>
  bid?: Maybe<MarketBid>
  offer?: Maybe<MarketOffer>
}

export type PunkInfo = {
  id: string
  owner?: Maybe<string>
  bid?: Maybe<MarketBid>
  offer?: Maybe<MarketOffer>
}

export type PunkInfoContainer = {
  startTimestamp: string
  endTimestamp: string
  refHeight: string
  punks: PunkInfo[]
}

export type PunkFilterValues = {
  sortBy: string
  orderBy: string
  search: string
}

export enum PunkSortType {
  ID = 0,
  RANK = 1
}

export type PunkDialogState = {
  type: PunkDialogType
  open: boolean
}

export enum PunkDialogType {
  BID = 0,
  OFFER = 1
}

export enum PunkEventType {
  Unknown,
  Mint,
  Offer,
  NewBid,
  CancelBid,
  Buy,
  CancelOffer
}

export type PunkEvent = {
  __typename: string
  id: string
  accountBlockHash: string
  accountBlockHeight: BigNumber
  timestamp: BigNumber
  eventType: number
  punkId: number
  amount?: Maybe<BigNumber>
  fromAddress?: Maybe<string>
  toAddress?: Maybe<string>
}

export type PunkEvents = {
  blockHeight: BigNumber
  events: PunkEvent[]
}

export type PunkChangedEvent = {
  accountBlockHash: string
  accountBlockHeight: BigNumber
  timestamp: BigNumber
  eventType: PunkEventType
  punkId: number
  amount?: Maybe<BigNumber>
  fromAddress?: Maybe<string>
  toAddress?: Maybe<string>
}

export type FilteredCollection<T> = {
  sortBy: string
  orderBy: string
  searchTerm?: string
  items: T[]
}

export enum MarketplaceTab {
  Items = 'items',
  Activity = 'activity'
}

export enum MarketEventType {
  Unknown,
  Start,
  Stop,
  MintPrice,
  ActionPrice
}

export type MarketChangedEvent = {
  timestamp: BigNumber
  eventType: MarketEventType
  value: BigNumber
}

export type ContractMarketInfo = {
  market_started: string
  mints_remaining: string
  mint_price: string
  action_price: string
}

export type MarketInfo = {
  marketStarted: boolean
  mintsRemaining: number
  mintPrice: BigNumber
  actionPrice: BigNumber
}

export type MarketOffer = {
  __typename: string
  itemIndex: BigNumber
  seller: string
  minAmount: BigNumber
  onlySellTo?: Maybe<string>
}

export type ContractMarketOffer = {
  isForSale: boolean
  itemIndex: BigNumber
  seller: string
  minAmount: BigNumber
  onlySellTo?: Maybe<string>
}

export type MarketBid = {
  __typename: string
  itemIndex: BigNumber
  bidder: string
  amount: BigNumber
}

export type ContractMarketBid = {
  hasBid: boolean
  itemIndex: BigNumber
  bidder: string
  amount: BigNumber
}

export enum GlobalEvent {
  ReInit = 'ReInit',
  DocumentVisibilityChanged = 'DocumentVisibilityChanged',
  ConfirmTransactionDialog = 'ConfirmTransactionDialog',
  ConnectWalletDialog = 'ConnectWalletDialog',
  QuotaChanged = 'QuotaChanged',
  PunkFilterValuesChanged = 'PunkFilterValuesChanged',
  MarketChanged = 'MarketChanged',
  PunkChanged = 'PunkChanged'
}

export enum ViteQuotaType {
  Account = "Account",
  Contract = "Contract"
}

export type ViteQuotaResult = {
  currentQuota: BigNumber
  maxQuota: BigNumber
  stakeAmount: BigNumber
}

export type ViteSubscriptionResult = {
  logs: any[],
  listener: any
}

export type ViteVmLogEvent = {
  accountBlockHash: string
  accountBlockHeight: string
  address: string
  vmlog: ViteVmLog
}

export type ViteVmLog = {
  data: string
  topics: string[]
}

export type VmLog = {
  event: string
  topic: string
  args: any
}

export enum VmLogEvent {
  MarketChanged = 'MarketChanged',
  PunkChanged = 'PunkChanged'
}