import GridOnIcon from '@mui/icons-material/GridOn';
import TimelineIcon from '@mui/icons-material/Timeline';
import { Box, Container, Tab, Tabs, Typography } from '@mui/material';
import React from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { MarketplaceTab } from '../../../../util/types';
import { MarketplaceBrowseActivity } from '../browse-activity';
import { MarketplaceBrowseItems } from '../browse-items';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`marketplace-tabpanel-${index}`}
      aria-labelledby={`marketplace-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 2 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

export const MarketplaceBrowse: React.FC = (props: any) => {
  const navigate = useNavigate();
  const { tab } = useParams();
  const getTab = (): number => {
    if (tab === MarketplaceTab.Activity) {
      return 1;
    } else {
      return 0;
    }
  }

  const initialValue = getTab();
  const [value, setValue] = React.useState<number>(initialValue);
  if (initialValue !== value) {
    setValue(initialValue)
  }

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
    switch (newValue) {
      case 1:
        navigate("/marketplace/activity");
        break;
      default:
        navigate("/marketplace");
        break;
    }
  };

  return (
    <>
      <Typography variant="h2" component="div" textAlign="center" sx={{ pt: 3 }} gutterBottom>
        Marketplace
      </Typography>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange}>
          <Tab icon={<GridOnIcon />} label="Items" value={0} />
          <Tab icon={<TimelineIcon />} label="Activity" value={1} />
        </Tabs>
      </Box>
      <Container sx={{ pb: 6 }} maxWidth="xl">
        <TabPanel value={value} index={0}>
          <MarketplaceBrowseItems></MarketplaceBrowseItems>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Box marginTop={5}>
            <MarketplaceBrowseActivity></MarketplaceBrowseActivity>
          </Box>
        </TabPanel>
      </Container>
    </>
  )
}
