import { gql } from "@apollo/client";
import { ApolloContext } from "../clients/apollo.client";
import { Punk, PunkEvents } from "../util/types";

export const PUNK_FIELDS = gql`
  fragment PunkFields on Punk {
    id
    rank
    image
    attributes
    owner
    bid {
      itemIndex
      bidder
      amount
    }
    offer {
      itemIndex
      seller
      minAmount
      onlySellTo
    }
  }
`;

export const PUNK_EVENT_FIELDS = gql`
  fragment PunkEventFields on PunkEvent {
    id
    accountBlockHash
    accountBlockHeight
    timestamp
    eventType
    punkId
    amount
    fromAddress
    toAddress
  }
`;

export const GET_PUNK_ATTRIBUTES_QUERY = gql`
  query GetPunkAttributes {
    punkAttributes @client
  }
`;

export const GET_PUNK_EVENTS_QUERY = gql`
  ${PUNK_EVENT_FIELDS}
  query GetPunkEvents($id: String!, $limit: Int!, $offset: Int!, $height: String) {
    punkEvents(id: $id, limit: $limit, offset: $offset, height: $height) @client {
      blockHeight
      events {
        ...PunkEventFields
      }
    }
  }
`;

export const GET_PUNK_OWNER_QUERY = gql`
  query GetPunkOwner($id: String!) {
    punkOwner(id: $id) @client
  }
`;

export const GET_PUNK_QUERY = gql`
  ${PUNK_FIELDS}
  query GetPunk($id: ID!) {
    punk(id: $id) @client {
      ...PunkFields
    }
  }
`;

export const GET_PUNKS_QUERY = gql`
  ${PUNK_FIELDS}
  query GetPunks($limit: Int!, $offset: Int!, $sort: PunkSortBy!, $order: OrderBy!, $search: String) {
    punks(limit: $limit, offset: $offset, sort: $sort, order: $order, search: $search) @client {
      ...PunkFields
    }
  }
`;

export const GET_PUNKS_ALL_QUERY = gql`
  ${PUNK_FIELDS}
  query GetPunksAll {
    punksAll @client {
      ...PunkFields
    }
  }
`;

export const GET_PUNKS_BY_ADDRESS_QUERY = gql`
  ${PUNK_FIELDS}
  query GetPunksByAddress($address: String!, $limit: Int!, $offset: Int!) {
    punksByAddress(address: $address, limit: $limit, offset: $offset) @client {
      ...PunkFields
    }
  }
`;

export const PunkQueries = {
  async punkAttributes(parent: any, params: any, context: ApolloContext): Promise<string[]> {
    const result = await context.client.datasource.getPunkAttributesAsync();
    return result;
  },
  async punkEvents(parent: any, { id, limit, offset, height }: any, context: ApolloContext): Promise<PunkEvents> {
    const result = await context.client.datasource.getPunkEventsAsync(id, limit, offset, height);
    return result;
  },
  async punkOwner(parent: any, { id }: any, context: ApolloContext): Promise<Maybe<string>> {
    const result = await context.client.datasource.getPunkOwnerAsync(id);
    return result;
  },
  async punk(parent: any, { id }: any, context: ApolloContext): Promise<Punk> {
    const result = await context.client.datasource.getPunkAsync(id);
    return result;
  },
  async punks(parent: any, { limit, offset, sort, order, search }: any, context: ApolloContext): Promise<Punk[]> {
    const result = await context.client.datasource.getPunksAsync(limit, offset, sort, order, search);
    return result;
  },
  async punksAll(parent: any, params: any, context: ApolloContext): Promise<Punk[]> {
    const result = await context.client.datasource.getPunksAllAsync();
    return result;
  },
  async punksByAddress(parent: any, { address, limit, offset }: any, context: ApolloContext): Promise<Punk[]> {
    const result = await context.client.datasource.getPunksByAddressAsync(address, limit, offset);
    return result;
  },
}