import { Document as FlexsearchDocument } from "flexsearch";
import { intersection, intersectionWith } from "lodash";
import { CommonUtil } from "../util/common.util";
import { getLogger } from "../util/logger";
import { Punk } from "../util/types";

const logger = getLogger();

export class PunkSearch {
  private _index!: FlexsearchDocument<Punk, false>

  constructor() {
    this.clear();
  }

  private clear(): void {
    this._index = new FlexsearchDocument({
      document: {
        id: "id",
        tag: "attributes",
        index: ["id"]
      },
      //preset: "memory",
      //tokenize: "forward"
    });
  }

  index(punks: Punk[]): void {
    if (punks) {
      this.clear();
      for (const p of punks) {
        this._index.add(p)
      }
    }
  }

  search(query: string, punks?: Maybe<Punk[]>): Maybe<Punk[]> {
    try {
      if (!punks || CommonUtil.isNullOrWhitespace(query)) {
        return punks;
      }
      if (CommonUtil.isNumber(query)) {
        const index = parseInt(query);
        if (index > 0) {
          const p = punks[index - 1];
          return !p ? [] : [p];
        }
      }
      const result = this._index.search({
        tag: query.split(','),
        limit: 10000
      });
      const mappedResult = result.map(e => e.result);
      if (mappedResult.length > 0) {
        if (mappedResult.length === 1) {
          return this.enrich(mappedResult[0].map(e => e.toString()), punks);
        } else {
          const intersectedResult = intersection(...mappedResult);
          return this.enrich(intersectedResult.map(e => e.toString()), punks);
        }
      }
      return undefined;
    } catch (error) {
      logger.error(error)();
      return undefined;
    }
  }

  private enrich(ids: string[], punks: Punk[]): Punk[] {
    return intersectionWith(punks, ids, (o, id) => o.id === id)
  }
}

const search = new PunkSearch();

export const getPunkSearch = () => {
  return search;
}