import { ApolloError, QueryResult, useQuery } from '@apollo/client';
import { Box, Container, Grid, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { useParams } from 'react-router-dom';
import { GET_PUNKS_BY_ADDRESS_QUERY } from '../../../../queries';
import { GetPunksByAddress, GetPunksByAddressVariables, GetPunksByAddress_punksByAddress } from '../../../../queries/__generated__/GetPunksByAddress';
import { FormatUtil } from '../../../../util/format.util';
import { Punk } from '../../../../util/types';
import { Alert } from '../../../common/components/alert';
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { MarketplaceBrowseItem } from '../../../marketplace/components/browse-item';

interface EmptyResultProps {
  error: ApolloError | undefined
  query: QueryResult<GetPunksByAddress, GetPunksByAddressVariables>
}

const EmptyResult: React.FC<EmptyResultProps> = (props: EmptyResultProps) => {
  return (
    <>
      {props.error ? (
        <Alert message={FormatUtil.formatMessage(props.error)} type="warning"></Alert>
      ) : (
        <>
          {props.query.loading || (props.query.data && props.query.data.punksByAddress.length > 0) ? (
            <></>
          ) : (
            <Alert message="No punks found" type="default" ></Alert>
          )}
        </>
      )}
    </>
  )
}

export const AccountDetails: React.FC = (props: any) => {
  const { address } = useParams();
  const limit = 8;
  const [isLoading, setIsLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [error, setError] = useState<any>(undefined);

  const query = useQuery<GetPunksByAddress, GetPunksByAddressVariables>(GET_PUNKS_BY_ADDRESS_QUERY, {
    variables: {
      address: address ?? "",
      offset: 0,
      limit: limit
    },
    fetchPolicy: 'network-only'
  });

  useEffect(() => {
    setIsLoading(query.loading);
  }, [query.loading]);

  useEffect(() => {
    setError(query.error);
  }, [query.error]);

  const fetchMoreDataAsync = async () => {
    const currentLength = query.data?.punksByAddress.length ?? 0;
    setIsLoading(true);
    try {
      const result = await query.fetchMore({
        variables: {
          offset: currentLength,
          limit: limit
        }
      });
      setHasMore(result.data.punksByAddress.length >= limit);
    } catch (error) {
      setError(error);
    }
    setIsLoading(false);
  };

  return (
    <>
      <Typography variant="h2" component="div" textAlign="center" sx={{ pt: 3 }}>
        Account
      </Typography>
      <Typography variant="subtitle1" component="div" textAlign="center" sx={{ wordBreak: "break-all" }} gutterBottom>
        {address}
      </Typography>
      <Container sx={{ pt: 3, pb: 6 }} maxWidth="xl">
        {isLoading || (query.data && query.data.punksByAddress.length > 0) ? (
          <>
            <InfiniteScroll
              dataLength={query.data?.punksByAddress.length ?? 0}
              next={fetchMoreDataAsync}
              hasMore={hasMore}
              loader={
                <Grid container spacing={3} justifyContent="center" alignItems="center">
                  <Grid item key='1' xs={12} md={6}>
                    {isLoading ? (
                      <Alert message="Loading..." type="default" ></Alert>
                    ) : (
                      <EmptyResult error={error} query={query}></EmptyResult>
                    )}
                  </Grid>
                </Grid>
              }
              scrollableTarget="mainscroll"
            >
              <Grid container spacing={3} justifyContent="center" alignItems="center" marginBottom={3}>
                {query.data?.punksByAddress.map((p: GetPunksByAddress_punksByAddress) => (
                  <Grid item key={p.id} xs={12} sm={6} md={4} lg={3}>
                    <MarketplaceBrowseItem item={p as Punk}></MarketplaceBrowseItem>
                  </Grid>
                ))}
              </Grid>
            </InfiniteScroll>
            {hasMore && !isLoading && (
              <Box sx={{ width: "100%", display: "flex", justifyContent: "center" }}>
                <ClickOnceButton variant="outlined" callbackFn={fetchMoreDataAsync} sx={{ minWidth: "200px" }}>Load more</ClickOnceButton>
              </Box>
            )}
          </>
        ) : (
          <Grid container spacing={3} justifyContent="center" alignItems="center">
            <Grid item key='1' xs={12} md={6}>
              <EmptyResult error={error} query={query}></EmptyResult>
            </Grid>
          </Grid>
        )}
      </Container>
    </>
  )
}
