import { FetchPolicy } from '@apollo/client';
import { BigNumber } from 'bignumber.js';
import React, { useCallback, useEffect, useState } from 'react';
import { getMarketService } from '../../../../services/market.service';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { TransformUtil } from '../../../../util/transform.util';
import { GlobalEvent, MarketChangedEvent, MarketInfo, Punk, PunkDialogState, PunkDialogType } from '../../../../util/types';
import { PunkBidDialog } from '../dialog-bid';
import { PunkOfferDialog } from '../dialog-offer';

const logger = getLogger();

interface Props {
  item: Punk
  state: PunkDialogState
  setState: React.Dispatch<React.SetStateAction<PunkDialogState>>
  refetchAsync: () => Promise<void>
}

export const PunkDialog: React.FC<Props> = (props: Props) => {
  const mounted = React.useRef(false);
  const [fee, setFee] = useState<BigNumber>(TransformUtil.defaultBigNumber);
  const service = getMarketService();
  const emitter = getEmitter();

  React.useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  const updateFee = useCallback(async (_fetchPolicy: FetchPolicy = "network-only") => {
    if (mounted.current) {
      setFee(TransformUtil.defaultBigNumber);
      try {
        const result = await service.getMarketInfoAsync(_fetchPolicy);
        if (mounted.current) {
          const info = result.data.marketInfo as MarketInfo;
          if (info && info.actionPrice.gt(0)) {
            setFee(info.actionPrice);
          }
        }
      } catch (error) {
        logger.error(error)();
      }
    }
  }, [service]);

  useEffect(() => {
    const handleEvent = (e: MarketChangedEvent) => {
      logger.info(`eventType: ${e.eventType} amount: ${e.value.toFixed()} timestamp: ${e.timestamp.toFixed()}`)();
      updateFee();
    }
    logger.info('emitter.on', GlobalEvent.MarketChanged)();
    emitter.on(GlobalEvent.MarketChanged, handleEvent);
    return () => {
      logger.info('emitter.off', GlobalEvent.MarketChanged)();
      emitter.off(GlobalEvent.MarketChanged, handleEvent);
    };
  }, [emitter, updateFee]);

  switch (props.state.type) {
    case PunkDialogType.BID:
      return <PunkBidDialog item={props.item} state={props.state} fee={fee} updateFee={updateFee} setState={props.setState} refetchAsync={props.refetchAsync}></PunkBidDialog>
    case PunkDialogType.OFFER:
      return <PunkOfferDialog item={props.item} state={props.state} fee={fee} updateFee={updateFee} setState={props.setState} refetchAsync={props.refetchAsync}></PunkOfferDialog>
    default:
      return <></>
  }
}
