import { ApolloClient, FetchPolicy, NormalizedCacheObject } from "@apollo/client";
import { getApolloClient } from "../clients/apollo.client";
import { MINT_MUTATION } from "../mutations";
import { Mint } from "../mutations/__generated__/Mint";
import { GET_MARKET_BID_QUERY, GET_MARKET_OFFER_QUERY, GET_PUNK_EVENTS_QUERY, GET_PUNK_OWNER_QUERY, GET_PUNK_QUERY } from "../queries";
import { GetMarketBid, GetMarketBidVariables } from "../queries/__generated__/GetMarketBid";
import { GetMarketOffer, GetMarketOfferVariables } from "../queries/__generated__/GetMarketOffer";
import { GetPunk, GetPunkVariables } from "../queries/__generated__/GetPunk";
import { GetPunkEvents, GetPunkEventsVariables } from "../queries/__generated__/GetPunkEvents";
import { GetPunkOwner, GetPunkOwnerVariables } from "../queries/__generated__/GetPunkOwner";
import { Punk, TypeNames } from "../util/types";

class PunkService {
  private readonly _apollo: ApolloClient<NormalizedCacheObject>;

  constructor() {
    this._apollo = getApolloClient();
  }

  getById = (_id: string, _fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetPunk, GetPunkVariables>({
      query: GET_PUNK_QUERY,
      variables: {
        id: _id
      },
      fetchPolicy: _fetchPolicy
    })
  }

  getOwnerAsync = (_id: string, _fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetPunkOwner, GetPunkOwnerVariables>({
      query: GET_PUNK_OWNER_QUERY,
      variables: {
        id: _id
      },
      fetchPolicy: _fetchPolicy
    })
  }

  getBidAsync = (_id: string, _fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetMarketBid, GetMarketBidVariables>({
      query: GET_MARKET_BID_QUERY,
      variables: {
        id: _id
      },
      fetchPolicy: _fetchPolicy
    })
  }

  getOfferAsync = (_id: string, _fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetMarketOffer, GetMarketOfferVariables>({
      query: GET_MARKET_OFFER_QUERY,
      variables: {
        id: _id
      },
      fetchPolicy: _fetchPolicy
    })
  }

  getEventsAsync = (_id: string, _offset: number, _limit: number, _height?: Maybe<string>, _fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetPunkEvents, GetPunkEventsVariables>({
      query: GET_PUNK_EVENTS_QUERY,
      variables: {
        id: _id,
        offset: _offset,
        limit: _limit,
        height: _height
      },
      fetchPolicy: _fetchPolicy
    })
  }

  getExtendedAsync = async (_punk: Punk, _fetchPolicy: FetchPolicy = "network-only") => {
    const defaultPunkExtended = {
      __typename: TypeNames.Punk,
      id: _punk.id,
      rank: _punk.rank,
      image: _punk.image,
      attributes: _punk.attributes
    };
    const ownerQuery = await this.getOwnerAsync(_punk.id, _fetchPolicy);
    const bidQuery = await this.getBidAsync(_punk.id, _fetchPolicy);
    const offerQuery = await this.getOfferAsync(_punk.id, _fetchPolicy);

    const punkExtended: Punk = {
      ...defaultPunkExtended,
      owner: ownerQuery.data.punkOwner,
      bid: bidQuery.data.marketBid,
      offer: offerQuery.data.marketOffer
    }

    const error = ownerQuery.error || bidQuery.error || offerQuery.error;
    const loading = ownerQuery.loading || bidQuery.loading || offerQuery.loading;

    return {
      punkExtended,
      ownerQuery,
      bidQuery,
      offerQuery,
      error,
      loading
    };
  }

  async mintAsync(): Promise<boolean> {
    const result = await this._apollo.mutate<Mint>({
      mutation: MINT_MUTATION
    });
    return Boolean(result.data?.mint ?? false);
  }
}

const service = new PunkService();

export const getPunkService = () => {
  return service;
}