import { Box, LinearProgress, Paper, Skeleton, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { CommonConstants } from '../../../../common/constants';
import { FormatUtil } from '../../../../util/format.util';

interface Props {
  loading: boolean
  mintsRemaining?: Maybe<number>
}

export const MintsRemaining: React.FC<Props> = (props: Props) => {
  const [pct, setPct] = useState(100);

  useEffect(() => {
    if (props.mintsRemaining && props.mintsRemaining > 0) {
      const minValue = props.mintsRemaining === CommonConstants.TOTAL_MINTS ? 100 : 90;
      setPct(Math.min(minValue, props.mintsRemaining / CommonConstants.TOTAL_MINTS * 100));
    } else {
      setPct(100);
    }
  }, [props.mintsRemaining]);

  return (
    <Paper elevation={3} sx={{ width: "60%", textAlign: "center", mb: 3 }}>
      <Box sx={{ px: 2, pt: 2, pb: 1.5 }}>
        {props.loading || !props.mintsRemaining ? (
          <Skeleton animation="wave" width="50%"></Skeleton>
        ) : (
          <Typography variant="subtitle1" color="textSecondary">
            <b>{FormatUtil.formatNumber(props.mintsRemaining)}</b> of {FormatUtil.formatNumber(CommonConstants.TOTAL_MINTS)} mintable
          </Typography>
        )}
      </Box>
      <LinearProgress sx={{ height: "8px" }} variant="determinate" color={pct <= 40 ? "error" : "primary"} value={pct} />
    </Paper>
  );
}
