import axios from "axios";
import { BigNumber } from "bignumber.js";
import { BaseDataSource } from ".";
import { getViteClient, ViteClient } from "../clients/vite.client";
import { CommonConfig } from "../common/config";
import { CommonConstants } from "../common/constants";
import { AbiItemType, getPunksContract, PunksContract } from "../contracts/punks.contract";
import { CommonUtil } from "../util/common.util";
import { getLogger } from "../util/logger";
import { TransformUtil } from "../util/transform.util";
import { MarketBid, MarketChangedEvent, MarketEventType, MarketInfo, MarketOffer, Network, Punk, PunkChangedEvent, PunkEvent, PunkEvents, PunkEventType, TypeNames, ViteQuotaResult, ViteVmLogEvent, VmLog, VmLogEvent } from "../util/types";

const logger = getLogger();

export class ViteDataSource extends BaseDataSource {
  private readonly _client: ViteClient;
  private readonly _contract: PunksContract;
  private _listener: any;

  constructor() {
    super();
    this._client = getViteClient();
    this._contract = getPunksContract();
  }

  async initPunkInfosAsync(): Promise<void> {
    try {
      const result = await axios({
        method: 'get',
        url: CommonConstants.MARKET_DATA_URL,
        responseType: 'json',
        timeout: 3000
      });
      const infos = result.data;
      this._punkInfos = this.getDefaultPunkInfos();
      if (infos) {
        for (const p of infos.punks) {
          const info = TransformUtil.toPunkInfo(p);
          if (info) {
            const index = parseInt(p.id) - 1;
            this._punkInfos[index] = info;
          }
        }
      }
      await this.updatePunkInfosAsync(TransformUtil.toBigNumber(infos.refHeight));
    } catch (error) {
      logger.error(error)();
    }
  }

  async initAsync(network: Network): Promise<void> {
    logger.info('Init ViteDataSource')();
    this.disposeProtected();
    await this._client.initAsync(network);
    await this._contract.initAsync(this._client, "./assets/contracts");
    this._listener = await this._client.createAddressListenerAsync(this._contract.contract.address);
    this._listener.on((results: ViteVmLogEvent[]) => {
      if (!this._contract?.contract.abi) {
        logger.error('Could not decode vmlog because contract abi is not defined.')();
      } else {
        for (let index = 0; index < results.length; index++) {
          const result = results[index];
          const vmLog = this._client.decodeVmLog(result.vmlog, this._contract.contract.abi);
          logger.info(vmLog ?? result)();
          if (vmLog) {
            this.handleVmLogAsync(result.accountBlockHash, result.accountBlockHeight, vmLog);
          }
        }
      }
    });
  }

  protected disposeProtected(): void {
    logger.info('Dispose ViteDataSource')();
    if (this._listener) {
      this._client.removeListener(this._listener);
    }
    this._client.dispose();
  }

  async getQuotaAsync(_address: string): Promise<ViteQuotaResult> {
    return this._client.getQuotaAsync(_address);
  }

  async getPunkEventsAsync(_id: string, _limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkEvents> {
    logger.info("id", _id, "limit", _limit, "offset", _offset, "height", _height)();
    if (CommonUtil.isNullOrWhitespace(_id)) {
      return {
        blockHeight: TransformUtil.defaultBigNumber,
        events: []
      };
    }
    const abiItem = this._contract.getAbiItemByType(AbiItemType.punkChangedEvent);
    const encodedLogSignature = this._client.encodeLogSignature(abiItem);
    const topics: string[][] = [
      [
        encodedLogSignature
      ],
      [
        // don't filter based on eventType
      ],
      [
        // filter based on punkId
        this._client.encodeParameter('uint256', _id)
      ]
    ]
    const fromHeight = this.getEventsFromHeight();
    const toHeight = await this.getAccountBlockHeightAsync(this._contract.contract.address, _height);
    let result = await this._client.getEventsByBlockAsync(this._contract.contract.address, fromHeight, toHeight, topics);
    result = result?.reverse().slice(_offset, _offset + _limit);
    const events = this.toPunkEvents(result);
    return {
      blockHeight: toHeight,
      events: events
    };
  }

  async getPunkOwnerAsync(_id: string): Promise<Maybe<string>> {
    const result = await this._contract.ownerOfAsync(_id);
    return result;
  }

  async getPunksByAddressAsync(_address: string, _limit: number, _offset: number): Promise<Punk[]> {
    logger.info("_address", _address, "limit", _limit, "offset", _offset)();
    if (CommonUtil.isNullOrWhitespace(_address)) {
      return [];
    }
    let result = await this._contract.getTokensOfAsync(_address);
    result = result.slice(_offset, _offset + _limit);
    const punks = [];
    for (const id of result) {
      const punk = await this.getPunkAsync(id);
      punks.push(punk)
    }
    return punks;
  }

  async getMarketBidAsync(_id: string): Promise<Maybe<MarketBid>> {
    const result = await this._contract.getBidAsync(_id);
    if (!result || !result.hasBid) {
      return null;
    }
    return {
      __typename: TypeNames.MarketBid,
      itemIndex: result.itemIndex,
      amount: result.amount,
      bidder: result.bidder
    };
  }

  async getMarketOfferAsync(_id: string): Promise<Maybe<MarketOffer>> {
    const result = await this._contract.getOfferAsync(_id);
    if (!result || !result.isForSale) {
      return null;
    }
    return {
      __typename: TypeNames.MarketOffer,
      itemIndex: result.itemIndex,
      minAmount: result.minAmount,
      seller: result.seller,
      onlySellTo: result.onlySellTo ?? ''
    };
  }

  async getMarketEventsAsync(_limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkEvents> {
    logger.info("limit", _limit, "offset", _offset)();
    const abiItem = this._contract.getAbiItemByType(AbiItemType.punkChangedEvent);
    const encodedLogSignature = this._client.encodeLogSignature(abiItem);
    const topics: string[][] = [
      [
        encodedLogSignature
      ]
    ]
    const toHeight = await this.getAccountBlockHeightAsync(this._contract.contract.address, _height);
    // Get max. last 1000 events
    const fromHeight = BigNumber.maximum(this.getEventsFromHeight(), toHeight.minus(1000));
    let result = await this._client.getEventsByBlockAsync(this._contract.contract.address, fromHeight, toHeight, topics);
    result = result?.reverse().slice(_offset, _offset + _limit);
    const events = this.toPunkEvents(result);
    return {
      blockHeight: toHeight,
      events: events
    };
  }

  async getMarketInfoAsync(): Promise<MarketInfo> {
    const result = await this._contract.getMarketInfoAsync();
    this._actionPrice = result.actionPrice;
    this._mintPrice = result.mintPrice;
    logger.info("getMarketInfoAsync", result)();
    return result;
  }

  async mintAsync(): Promise<boolean> {
    await this.checkQuotaAsync();
    const mintPrice = await this.getMintPriceAsync();
    const result = await this._contract.mintAsync(this.getAccount(), mintPrice.toFixed());
    logger.info("mintAsync", result)();
    return result.status === 0;
  }

  async offerAsync(_id: string, _amount: string, _address?: string | null): Promise<boolean> {
    logger.info("offerAsync", "id", _id, "amount", _amount, "address", _address)();
    await this.checkQuotaAsync();
    const fee = await this.getFeeAsync();
    let result;
    if (_address && !CommonUtil.isNullOrWhitespace(_address)) {
      result = await this._contract.offerToAddressAsync(this.getAccount(), _id, _amount, _address, fee);
    } else {
      result = await this._contract.offerAsync(this.getAccount(), _id, _amount, fee);
    }
    logger.info("offerAsync", result)();
    return result.status === 0;
  }

  async cancelOfferAsync(_id: string): Promise<boolean> {
    logger.info("cancelOfferAsync", "id", _id)();
    await this.checkQuotaAsync();
    const result = await this._contract.cancelOfferAsync(this.getAccount(), _id);
    logger.info("cancelOfferAsync", result)();
    return result.status === 0;
  }

  async buyAsync(_id: string, _amount: string): Promise<boolean> {
    await this.checkQuotaAsync();
    logger.info("buyAsync", "id", _id, "amount", _amount)();
    const result = await this._contract.buyAsync(this.getAccount(), _id, _amount);
    logger.info("buyAsync", result)();
    return result.status === 0;
  }

  async bidAsync(_id: string, _amount: string): Promise<boolean> {
    logger.info("bidAsync", "id", _id, "amount", _amount)();
    await this.checkQuotaAsync();
    const fee = await this.getFeeAsync();
    const result = await this._contract.enterBidAsync(this.getAccount(), _id, _amount, fee);
    logger.info(result)();
    return result.status === 0;
  }

  async acceptBidAsync(_id: string, _amount: string): Promise<boolean> {
    logger.info("acceptBidAsync", "id", _id, "amount", _amount)();
    await this.checkQuotaAsync();
    const result = await this._contract.acceptBidAsync(this.getAccount(), _id, _amount);
    logger.info("acceptBidAsync", result)();
    return result.status === 0;
  }

  async cancelBidAsync(_id: string): Promise<boolean> {
    logger.info("cancelBidAsync", "id", _id)();
    await this.checkQuotaAsync();
    const result = await this._contract.cancelBidAsync(this.getAccount(), _id);
    logger.info("cancelBidAsync", result)();
    return result.status === 0;
  }

  private async handleVmLogAsync(accountBlockHash: string, accountBlockHeight: string, vmlog: VmLog): Promise<void> {
    try {
      switch (vmlog.event) {
        case VmLogEvent.MarketChanged:
          const marketEvent: MarketChangedEvent = {
            timestamp: new BigNumber(vmlog.args.timestamp),
            eventType: parseInt(vmlog.args.eventType),
            value: new BigNumber(vmlog.args.value)
          }
          switch (marketEvent.eventType) {
            case MarketEventType.ActionPrice:
              this._actionPrice = marketEvent.value;
              break;
            case MarketEventType.MintPrice:
              this._mintPrice = marketEvent.value;
              break;
            default:
              break;
          }
          this._emitter.emitMarketChanged(marketEvent);
          break;
        case VmLogEvent.PunkChanged:
          const punkEvent: PunkChangedEvent = {
            accountBlockHash: accountBlockHash,
            accountBlockHeight: new BigNumber(accountBlockHeight),
            timestamp: new BigNumber(vmlog.args.timestamp),
            eventType: parseInt(vmlog.args.eventType),
            punkId: parseInt(vmlog.args.punkId),
            amount: new BigNumber(vmlog.args.amount),
            fromAddress: vmlog.args.fromAddress,
            toAddress: vmlog.args.toAddress
          }
          this._emitter.emitPunkChanged(punkEvent);
          break;
        default:
          break;
      }
    } catch (error) {
      logger.error(error)();
    }
  }

  private toPunkEvents(events: ViteVmLogEvent[]): PunkEvent[] {
    if (!Array.isArray(events)) {
      return [];
    }
    const result: PunkEvent[] = [];
    for (const e of events) {
      const transformed = this.toPunkEvent(e);
      if (transformed) {
        result.push(transformed);
      }
    }
    return result;
  }

  private toPunkEvent(event: ViteVmLogEvent): Maybe<PunkEvent> {
    if (!event) {
      return null;
    }
    const abiItem = this._contract.getAbiItemByType(AbiItemType.punkChangedEvent);
    const decoded = this._client.decodeVmLogByAbiItem(event.vmlog, abiItem);
    if (!decoded) {
      return null;
    }
    const transformed = {
      accountBlockHash: event.accountBlockHash,
      accountBlockHeight: event.accountBlockHeight,
      timestamp: decoded.args.timestamp,
      eventType: decoded.args.eventType,
      punkId: decoded.args.punkId,
      amount: decoded.args.amount,
      fromAddress: decoded.args.fromAddress,
      toAddress: decoded.args.toAddress
    };
    return TransformUtil.toPunkEvent(transformed);
  }

  private async getFeeAsync(): Promise<Maybe<string>> {
    const actionPrice = await this.getActionPriceAsync();
    if (actionPrice.gt(0)) {
      return actionPrice.toFixed();
    }
    return null;
  }

  private async getAccountBlockHeightAsync(_address: string, _height?: Maybe<string>): Promise<BigNumber> {
    let height = new BigNumber("0");
    if (_height && !CommonUtil.isNullOrWhitespace(_height)) {
      height = new BigNumber(_height);
    } else {
      height = await this._client.getAccountBlockHeightAsync(_address);
    }
    return height;
  }

  private getEventsFromHeight(): BigNumber {
    if (CommonConfig.IS_PRODUCTION) {
      // Hide events before official launch
      // return new BigNumber("1150");
    }
    return new BigNumber("0");
  }

  private async checkQuotaAsync(): Promise<void> {
    const address = this._contract.contract.address;
    const quota = await this.getQuotaAsync(address);
    this._emitter.emitQuotaChanged(address, quota);
  }

  private async updatePunkInfosAsync(_refHeight: BigNumber): Promise<void> {
    if (!this._punkInfos || this._punkInfos.length !== CommonConstants.TOTAL_MINTS) {
      logger.info("Invalid market data provided", this._punkInfos?.length)();
      return;
    }
    logger.info("Get market events with reference height", _refHeight.toFixed())();
    const events = await this.getNewMarketEventsAsync(_refHeight);
    if (!Array.isArray(events)) {
      logger.info("Get market events result is not an array")();
      return;
    }
    logger.info("events.length", events.length)();
    for (const e of events) {
      const punkEvent = this.toPunkEvent(e);
      if (!punkEvent) {
        logger.info("Invalid punk event", e)();
        continue;
      }
      const punk = this._punkInfos[punkEvent.punkId - 1];
      switch (punkEvent.eventType) {
        case PunkEventType.Mint:
          punk.owner = punkEvent.toAddress;
          break;
        case PunkEventType.Offer:
          punk.offer = {
            __typename: TypeNames.MarketOffer,
            itemIndex: TransformUtil.toBigNumber(punkEvent.id),
            seller: punkEvent.fromAddress ?? "",
            minAmount: punkEvent.amount ?? TransformUtil.defaultBigNumber,
            onlySellTo: punkEvent.toAddress
          }
          break;
        case PunkEventType.NewBid:
          punk.bid = {
            __typename: TypeNames.MarketBid,
            itemIndex: TransformUtil.toBigNumber(punkEvent.id),
            bidder: punkEvent.fromAddress ?? "",
            amount: punkEvent.amount ?? TransformUtil.defaultBigNumber
          }
          break;
        case PunkEventType.CancelBid:
          punk.bid = undefined;
          break;
        case PunkEventType.CancelOffer:
          punk.offer = undefined;
          break;
        case PunkEventType.Buy:
          punk.owner = punkEvent.toAddress;
          break;
        default:
          break;
      }
    }
  }

  private getNewMarketEventsAsync = async (_from: BigNumber) => {
    const abiItem = this._contract.getAbiItemByType(AbiItemType.punkChangedEvent);
    const encodedLogSignature = this._client.encodeLogSignature(abiItem);
    const topics: string[][] = [
      [
        encodedLogSignature
      ]
    ]
    const toHeight = await this._client.getAccountBlockHeightAsync(this._contract.contract.address);
    return await this._client.getEventsByBlockAsync(this._contract.contract.address, _from, toHeight, topics);
  }
}

const ds = new ViteDataSource();

export const getViteDataSource = () => {
  return ds;
}