import LaunchIcon from '@mui/icons-material/Launch';
import { Alert, Button, Container, Stack, Typography } from '@mui/material';
import { styled } from '@mui/system';
import BigNumber from 'bignumber.js';
import React, { useEffect, useState } from 'react';
import { CommonConstants } from '../../../../common/constants';
import { getGeneralService } from '../../../../services/general.service';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { TokenUtil } from '../../../../util/token.util';
import { GlobalEvent, ViteQuotaResult } from '../../../../util/types';
import { BootstrapTooltip } from '../../../common/components/tooltip';

const logger = getLogger();

const QuotaContainer = styled(Container)(({ theme }) => (`
  position: absolute;
  top: 64px;
  right: 0;
  width: 100%;
  z-index: 9999;
`));

const QuotaAlert = styled(Alert)(({ theme }) => (`
  float: right;
  background-color: rgba(255, 255, 255);
`));

export const ContractQuota: React.FC = (props: any) => {
  const mounted = React.useRef(false);
  const [quota, setQuota] = useState<ViteQuotaResult>({
    currentQuota: new BigNumber("0"),
    maxQuota: new BigNumber("0"),
    stakeAmount: new BigNumber("0")
  });
  const [showContainer, setShowContainer] = useState<boolean>(false);
  const service = getGeneralService();
  const emitter = getEmitter();

  React.useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (showContainer) {
      interval = setInterval(async () => {
        const result = await service.getContractQuota();
        logger.info(result.data.quota)();
        if (mounted.current && result.data.quota) {
          const quotaResult = result.data.quota as ViteQuotaResult;
          if (quotaResult.currentQuota.gte(quotaResult.maxQuota)) {
            clearInterval(interval);
            setShowContainer(false);
          }
          setQuota({
            currentQuota: quotaResult.currentQuota,
            maxQuota: quotaResult.maxQuota,
            stakeAmount: quotaResult.stakeAmount
          });
        }
      }, 2000);
    }
    return () => {
      logger.info("clearInterval(interval)")();
      if (interval) {
        clearInterval(interval);
      }
    }
  }, [service, showContainer]);

  useEffect(() => {
    const handleEvent = (_address: string, _quota: ViteQuotaResult) => {
      if (mounted.current && CommonConstants.QUOTA_THRESHOLD.gte(_quota.currentQuota.minus(CommonConstants.QUOTA_THRESHOLD))) {
        setQuota({
          currentQuota: _quota.currentQuota,
          maxQuota: _quota.maxQuota,
          stakeAmount: _quota.stakeAmount
        });
        setShowContainer(true);
      }
    }
    emitter.on(GlobalEvent.QuotaChanged, handleEvent);
    return () => {
      logger.info("emitter.off(GlobalEvent.QuotaChanged, handleEvent)")();
      emitter.off(GlobalEvent.QuotaChanged, handleEvent);
    };
  }, [emitter]);

  const formatBigNumber = (bn: BigNumber, decimals: number = CommonConstants.CURRENCY_DECIMALS) => {
    return TokenUtil.formatBigNumber(bn, 0, decimals);
  }

  if (!showContainer || quota.maxQuota.eq(0)) {
    return (
      <>
      </>
    )
  }

  return (
    <QuotaContainer maxWidth="xl">
      <QuotaAlert icon={false} severity="info" onClose={() => { setShowContainer(false) }} sx={{ mt: 3, mr: 2 }}>
        <Typography marginBottom={1} paragraph>
          <b>Contract quota is recharging...</b>
        </Typography>
        <BootstrapTooltip title={formatBigNumber(quota.currentQuota)} placement="left" arrow>
          <Typography marginBottom={1} paragraph>
            {formatBigNumber(quota.currentQuota, 6)} / {formatBigNumber(quota.maxQuota, 6)}
          </Typography>
        </BootstrapTooltip>
        <Typography marginBottom={1}>You can help by staking {CommonConstants.CURRENCY_NAME}</Typography>
        <Stack spacing={1} direction="row">
          <Typography>What does this mean?</Typography>
          <Button variant="outlined" size="small" href={CommonConstants.QUOTA_WIKI_URL} target="_blank" endIcon={<LaunchIcon />}>
            More info
          </Button>
        </Stack>
      </QuotaAlert>
    </QuotaContainer>
  )
}
