import { useQuery } from '@apollo/client';
import { Container, Grid } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { GET_PUNKS_ALL_QUERY } from '../../../../queries';
import { GetPunksAll } from '../../../../queries/__generated__/GetPunksAll';
import { getLogger } from '../../../../util/logger';
import { Punk } from '../../../../util/types';
import { MarketplaceBrowseItem } from '../../../marketplace/components/browse-item';

const logger = getLogger();

export const TestList = () => {
  const [allPunks, setAllPunks] = useState<Punk[]>([]);
  const [punks, setPunks] = useState<Punk[]>([]);

  useQuery<GetPunksAll>(GET_PUNKS_ALL_QUERY, {
    fetchPolicy: 'network-only',
    onCompleted: (data) => {
      setAllPunks(data.punksAll);
    }
  });

  useEffect(() => {
    const ids = new Set([
      1631,
      2105,
      2203,
      3073,
      4171,
      4191,
      6819,
      7552,
      7864,
      8936,
      9650,
      9724
    ]);
    let filtered = [];
    for (const p of allPunks) {
      if (ids.has(parseInt(p.id))) {
        filtered.push(p);
      }
    }
    filtered = filtered.reverse();
    setPunks(filtered);
    logger.info(filtered.length, allPunks.length)();
  }, [allPunks]);

  return (
    <>
      <Container sx={{ pb: 6 }} maxWidth="xl">
        <Grid container spacing={3} justifyContent="center" alignItems="center" marginBottom={3}>
          {punks.map((p: Punk) => (
            <Grid item key={p.id} xs={12} sm={6} md={4} lg={3}>
              <MarketplaceBrowseItem item={p}></MarketplaceBrowseItem>
            </Grid>
          ))}
        </Grid>
      </Container>
    </>
  );
}
