import { useQuery } from '@apollo/client';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Avatar, Grid, Link, Paper, styled, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { BigNumber } from 'bignumber.js';
import React from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Link as RouterLink } from 'react-router-dom';
import { CommonConfig } from '../../../../common/config';
import { CommonConstants, PunkEventTypes } from '../../../../common/constants';
import { GET_PUNK_QUERY } from '../../../../queries';
import { GetPunk, GetPunkVariables } from '../../../../queries/__generated__/GetPunk';
import { CommonUtil } from '../../../../util/common.util';
import { FormatUtil } from '../../../../util/format.util';
import { IconUtil } from '../../../../util/icon.util';
import { MomentUtil } from '../../../../util/moment.util';
import { TokenUtil } from '../../../../util/token.util';
import { PunkEvent, PunkEventType } from '../../../../util/types';
import { UrlUtil } from '../../../../util/url.util';
import { Alert } from '../alert';
import { ExplorerLink, ExplorerLinkType } from '../explorer-link';
import { ExtendedBootstrapTooltip } from '../tooltip';

interface EventTableRowProps {
  item: PunkEvent
}

const CustomAvatar = styled(Avatar)(({ theme }) => (`
  background-image: url('${CommonConstants.ITEM_BG_URL}');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  image-rendering: pixelated;
  margin-right: 10px;
`));

const CustomTableCell = styled(TableCell)(({ theme }) => (`
  background-color: rgba(255, 255, 255, 0.3);
`));

const EventTableRow: React.FC<EventTableRowProps> = (props: EventTableRowProps) => {
  const query = useQuery<GetPunk, GetPunkVariables>(GET_PUNK_QUERY, {
    variables: {
      id: props.item.punkId.toString()
    }
  });
  const momentUtil = new MomentUtil();

  const formatTimestamp = (timestamp: any) => {
    return momentUtil.getLocalReverseFormatted(momentUtil.getFromUnix(timestamp));
  }

  const formatViteTooltip = (bn: BigNumber) => {
    return TokenUtil.formatViteTooltip(bn);
  }

  const formatVite = (bn: BigNumber) => {
    return TokenUtil.formatVite(bn);
  }

  const isPreMint = () => {
    if (props.item.eventType !== PunkEventType.Mint || !CommonConfig.PRE_MINT_HEIGHT) {
      return false;
    }
    return props.item.accountBlockHeight.lte(CommonConfig.PRE_MINT_HEIGHT);
  }

  const getEventName = (): string => {
    const eventType = props.item.eventType as PunkEventType;
    switch (eventType) {
      case PunkEventType.Offer:
        if (!props.item.amount || props.item.amount.lte(0)) {
          return "Gift";
        }
        break;
      case PunkEventType.Buy:
        if (!props.item.amount || props.item.amount.lte(0)) {
          return "Claim gift";
        }
        break;
      default:
        break;
    }
    return PunkEventTypes[props.item.eventType].name;
  }

  return (
    <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
      <TableCell component="th" scope="row">
        <FontAwesomeIcon icon={IconUtil.getPunkEventTypeIcon(props.item.eventType as PunkEventType, props.item.amount)} /> <b>{getEventName()}</b>
      </TableCell>
      <TableCell>
        <Link underline="hover" component={RouterLink} to={`/punk/${props.item.punkId}`} display="flex" alignItems="center">
          <CustomAvatar variant="rounded" src={UrlUtil.getPunkUrl(query.data?.punk.image)}>
          </CustomAvatar>
          {CommonConstants.PUNK_NAME} #{props.item.punkId}
        </Link>
      </TableCell>
      <TableCell>
        {props.item.amount?.gt(0) ? (
          <>
            <ExtendedBootstrapTooltip title={formatViteTooltip(props.item.amount)}>
              <span>{formatVite(props.item.amount)}</span>
            </ExtendedBootstrapTooltip>
          </>
        ) : (
          <>
          </>
        )}
      </TableCell>
      <TableCell>
        <ExplorerLink type={ExplorerLinkType.Address} value={props.item.fromAddress}>
          <ExtendedBootstrapTooltip title={props.item.fromAddress ?? ""}>
            <span>{CommonUtil.truncateStringInTheMiddle(props.item.fromAddress, 10, 5)}</span>
          </ExtendedBootstrapTooltip>
        </ExplorerLink>
      </TableCell>
      <TableCell>
        <ExplorerLink type={ExplorerLinkType.Address} value={props.item.toAddress}>
          <ExtendedBootstrapTooltip title={props.item.toAddress ?? ""}>
            <span>{CommonUtil.truncateStringInTheMiddle(props.item.toAddress, 10, 5)}</span>
          </ExtendedBootstrapTooltip>
        </ExplorerLink>
      </TableCell>
      <TableCell align="right">
        {isPreMint() ? (
          <>
            {formatTimestamp(props.item.timestamp.toFixed())}
          </>
        ) : (
          <ExplorerLink type={ExplorerLinkType.Transaction} value={props.item.accountBlockHash}>
            {formatTimestamp(props.item.timestamp.toFixed())}
          </ExplorerLink>
        )}
      </TableCell>
    </TableRow>
  )
}

const EventListLoading: React.FC<Props> = (props: Props) => {
  return (
    <Grid item key='1' xs={12} md={6}>
      {props.loading ? (
        <Alert message="Loading..." type="default" ></Alert>
      ) : (
        <>
          {props.error ? (
            <Alert message={FormatUtil.formatMessage(props.error)} type="warning"></Alert>
          ) : (
            <>
              {props.events.length > 0 ? (
                <></>
              ) : (
                <Alert message="No activity yet" type="default"></Alert>
              )}
            </>
          )}
        </>
      )}
    </Grid>
  )
}

interface Props {
  events: PunkEvent[]
  newEvents: PunkEvent[]
  error: any
  loading: boolean
  hasMore: boolean
  height?: number
  fetchMoreDataAsync(): Promise<void>
}

export const EventList: React.FC<Props> = (props: Props) => {
  return (
    <Paper
      id="event-list"
      style={{
        height: props.height ?? 300,
        overflow: 'auto',
        backgroundColor: 'rgba(255, 255, 255, 0.5)'
      }}
    >
      {props.newEvents.length > 0 || props.events.length > 0 ? (
        <InfiniteScroll
          dataLength={props.events.length ?? 0}
          next={props.fetchMoreDataAsync}
          hasMore={props.hasMore}
          loader={
            <Grid container spacing={3} justifyContent="center" alignItems="center">
              <EventListLoading {...props}></EventListLoading>
            </Grid>
          }
          scrollableTarget="event-list"
        >
          <TableContainer>
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  <CustomTableCell></CustomTableCell>
                  <CustomTableCell><b>Punk</b></CustomTableCell>
                  <CustomTableCell><b>Amount</b></CustomTableCell>
                  <CustomTableCell><b>From</b></CustomTableCell>
                  <CustomTableCell><b>To</b></CustomTableCell>
                  <CustomTableCell align="right"><b>Date</b></CustomTableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {props.newEvents.map((e: PunkEvent) => (
                  <EventTableRow key={`new-events-${e.id}`} item={e} />
                ))}
                {props.events.map((e: PunkEvent) => (
                  <EventTableRow key={`events-${e.id}`} item={e} />
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </InfiniteScroll>) : (
        <Grid sx={{ height: "100%" }} container spacing={3} justifyContent="center" alignItems="center">
          <EventListLoading {...props}></EventListLoading>
        </Grid>
      )}
    </Paper>
  )
}
