import { gql } from "@apollo/client";
import { ApolloContext } from "../clients/apollo.client";

export const OFFER_MUTATION = gql`
  mutation Offer($id: ID!, $amount: String!, $address: String) {
    offer(id: $id, amount: $amount, address: $address) @client
  }
`;

export const CANCEL_OFFER_MUTATION = gql`
  mutation CancelOffer($id: ID!) {
    cancelOffer(id: $id) @client
  }
`;

export const BUY_MUTATION = gql`
  mutation Buy($id: ID!, $amount: String!) {
    buy(id: $id, amount: $amount) @client
  }
`;

export const BID_MUTATION = gql`
  mutation Bid($id: ID!, $amount: String!) {
    bid(id: $id, amount: $amount) @client
  }
`;

export const ACCEPT_BID_MUTATION = gql`
  mutation AcceptBid($id: ID!, $amount: String!) {
    acceptBid(id: $id, amount: $amount) @client
  }
`;

export const CANCEL_BID_MUTATION = gql`
  mutation CancelBid($id: ID!) {
    cancelBid(id: $id) @client
  }
`;

export const MarketMutations = {
  async offer(parent: any, { id, amount, address }: any, context: ApolloContext): Promise<boolean> {
    return context.client.datasource.offerAsync(id, amount, address);
  },
  async cancelOffer(parent: any, { id }: any, context: ApolloContext): Promise<boolean> {
    return context.client.datasource.cancelOfferAsync(id);
  },
  async buy(parent: any, { id, amount }: any, context: ApolloContext): Promise<boolean> {
    return context.client.datasource.buyAsync(id, amount);
  },
  async bid(parent: any, { id, amount }: any, context: ApolloContext): Promise<boolean> {
    return context.client.datasource.bidAsync(id, amount);
  },
  async acceptBid(parent: any, { id, amount }: any, context: ApolloContext): Promise<boolean> {
    return context.client.datasource.acceptBidAsync(id, amount);
  },
  async cancelBid(parent: any, { id }: any, context: ApolloContext): Promise<boolean> {
    return context.client.datasource.cancelBidAsync(id);
  },
}