import { ApolloClient, FetchPolicy, NormalizedCacheObject } from "@apollo/client";
import { getApolloClient } from "../clients/apollo.client";
import { ACCEPT_BID_MUTATION, BID_MUTATION, BUY_MUTATION, CANCEL_BID_MUTATION, CANCEL_OFFER_MUTATION, OFFER_MUTATION } from "../mutations";
import { AcceptBid, AcceptBidVariables } from "../mutations/__generated__/AcceptBid";
import { Bid, BidVariables } from "../mutations/__generated__/Bid";
import { Buy, BuyVariables } from "../mutations/__generated__/Buy";
import { CancelBid, CancelBidVariables } from "../mutations/__generated__/CancelBid";
import { CancelOffer, CancelOfferVariables } from "../mutations/__generated__/CancelOffer";
import { Offer, OfferVariables } from "../mutations/__generated__/Offer";
import { GET_MARKET_EVENTS_QUERY, GET_MARKET_INFO_QUERY } from "../queries";
import { GetMarketEvents, GetMarketEventsVariables } from "../queries/__generated__/GetMarketEvents";
import { GetMarketInfo } from "../queries/__generated__/GetMarketInfo";
import { CommonUtil } from "../util/common.util";

class MarketService {
  private readonly _apollo: ApolloClient<NormalizedCacheObject>;

  constructor() {
    this._apollo = getApolloClient();
  }

  getMarketInfoAsync = (_fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetMarketInfo>({
      query: GET_MARKET_INFO_QUERY,
      fetchPolicy: _fetchPolicy
    })
  }

  getEventsAsync = (_offset: number, _limit: number, _height?: Maybe<string>, _fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetMarketEvents, GetMarketEventsVariables>({
      query: GET_MARKET_EVENTS_QUERY,
      variables: {
        offset: _offset,
        limit: _limit,
        height: _height
      },
      fetchPolicy: _fetchPolicy
    })
  }

  async offerAsync(id: string, amount: string, address?: Maybe<string>): Promise<boolean> {
    const result = await this._apollo.mutate<Offer, OfferVariables>({
      mutation: OFFER_MUTATION,
      variables: {
        id: id,
        amount: amount,
        address: CommonUtil.isNullOrWhitespace(address) ? undefined : address
      }
    });
    return Boolean(result.data?.offer ?? false);
  }

  async cancelOfferAsync(id: string): Promise<boolean> {
    const result = await this._apollo.mutate<CancelOffer, CancelOfferVariables>({
      mutation: CANCEL_OFFER_MUTATION,
      variables: {
        id: id
      }
    });
    return Boolean(result.data?.cancelOffer ?? false);
  }

  async buyAsync(id: string, amount: string): Promise<boolean> {
    const result = await this._apollo.mutate<Buy, BuyVariables>({
      mutation: BUY_MUTATION,
      variables: {
        id: id,
        amount: amount
      }
    });
    return Boolean(result.data?.buy ?? false);
  }

  async bidAsync(id: string, amount: string): Promise<boolean> {
    const result = await this._apollo.mutate<Bid, BidVariables>({
      mutation: BID_MUTATION,
      variables: {
        id: id,
        amount: amount
      }
    });
    return Boolean(result.data?.bid ?? false);
  }

  async acceptBidAsync(id: string, amount: string): Promise<boolean> {
    const result = await this._apollo.mutate<AcceptBid, AcceptBidVariables>({
      mutation: ACCEPT_BID_MUTATION,
      variables: {
        id: id,
        amount: amount
      }
    });
    return Boolean(result.data?.acceptBid ?? false);
  }

  async cancelBidAsync(id: string): Promise<boolean> {
    const result = await this._apollo.mutate<CancelBid, CancelBidVariables>({
      mutation: CANCEL_BID_MUTATION,
      variables: {
        id: id
      }
    });
    return Boolean(result.data?.cancelBid ?? false);
  }
}

const service = new MarketService();

export const getMarketService = () => {
  return service;
}