import BigNumber from "bignumber.js";
import { CommonConfig } from "../common/config";
import { CommonUtil } from "../util/common.util";
import { CompressUtil } from "../util/compress.util";
import { getLogger } from "../util/logger";
import { TransformUtil } from "../util/transform.util";
import { ContractMarketBid, ContractMarketInfo, ContractMarketOffer, MarketEventType, MarketInfo } from "../util/types";
import { WalletAccount } from "../wallet";
import { BaseContract } from "./base.contract";

const logger = getLogger();

export enum AbiItemType {
  getDeveloper,
  getMarketInfo,
  isContract,
  balanceOf,
  ownerOf,
  getMappedIndex,
  totalSupply,
  getTokensOf,
  tokenURI,
  getBaseUrl,
  getOffer,
  getBid,
  transferEvent,
  marketChangedEvent,
  punkChangedEvent
}

export class PunksContract extends BaseContract {
  private _abiMap: Map<AbiItemType, any>;

  constructor() {
    super();
    this._abiMap = new Map<AbiItemType, any>();
  }

  protected async initAsyncProtected(basePath: string): Promise<void> {
    const punksContract = await this._fileUtil.readFileAsync(CommonUtil.appendPath(basePath, "Punks.gz"), "binary");
    this._contract = JSON.parse(CompressUtil.decompress(CommonUtil.toUint8Array(punksContract, "binary")));
    this._contract.address = CommonConfig.CONTRACT_ADDRESS;
    this._abiMap.set(AbiItemType.getDeveloper, this.getAbiItem("offchain", "getDeveloper"));
    this._abiMap.set(AbiItemType.getMarketInfo, this.getAbiItem("offchain", "getMarketInfo"));
    this._abiMap.set(AbiItemType.isContract, this.getAbiItem("offchain", "isContract"));
    this._abiMap.set(AbiItemType.balanceOf, this.getAbiItem("offchain", "balanceOf"));
    this._abiMap.set(AbiItemType.ownerOf, this.getAbiItem("offchain", "ownerOf"));
    this._abiMap.set(AbiItemType.getMappedIndex, this.getAbiItem("offchain", "getMappedIndex"));
    this._abiMap.set(AbiItemType.totalSupply, this.getAbiItem("offchain", "totalSupply"));
    this._abiMap.set(AbiItemType.getTokensOf, this.getAbiItem("offchain", "getTokensOf"));
    this._abiMap.set(AbiItemType.tokenURI, this.getAbiItem("offchain", "tokenURI"));
    this._abiMap.set(AbiItemType.getBaseUrl, this.getAbiItem("offchain", "getBaseUrl"));
    this._abiMap.set(AbiItemType.getOffer, this.getAbiItem("offchain", "getOffer"));
    this._abiMap.set(AbiItemType.getBid, this.getAbiItem("offchain", "getBid"));
    this._abiMap.set(AbiItemType.transferEvent, this.getAbiItem("event", "Transfer"));
    this._abiMap.set(AbiItemType.marketChangedEvent, this.getAbiItem("event", "MarketChanged"));
    this._abiMap.set(AbiItemType.punkChangedEvent, this.getAbiItem("event", "PunkChanged"));
  }

  private getAbiItem(_type: string, _name: string): any {
    return this._contract.abi.find(e => e.type === _type && e.name === _name);
  }

  getAbiItemByType(_abiItemType: AbiItemType): any {
    return this._abiMap.get(_abiItemType);
  }

  private async callOffChainMethodAsync(_abiItemType: AbiItemType, _params: any): Promise<any> {
    return this._client.callOffChainMethodAsync(this._contract.address, this.getAbiItemByType(_abiItemType), this._contract.offChain, _params);
  }

  async setMintPriceAsync(_account: WalletAccount, _price: string): Promise<any> {
    logger.info("setMintPriceAsync", _price)();
    return this.callContractAsync(_account, 'setPrice', [MarketEventType.MintPrice, _price]);
  }

  async setActionPriceAsync(_account: WalletAccount, _price: string): Promise<any> {
    logger.info("setActionPriceAsync", _price)();
    return this.callContractAsync(_account, 'setPrice', [MarketEventType.ActionPrice, _price]);
  }

  async getDeveloperAsync(): Promise<any> {
    const result = await this.callOffChainMethodAsync(AbiItemType.getDeveloper, []);
    if (!result) {
      return undefined;
    }
    const value = result[0].value;
    logger.info("getDeveloperAsync", value)();
    return value;
  }

  async setDeveloperAsync(_account: WalletAccount, _address: string): Promise<any> {
    logger.info("setDeveloperAsync", _address)();
    return this.callContractAsync(_account, 'setDeveloper', [_address]);
  }

  async getMarketInfoAsync(): Promise<MarketInfo> {
    const result = await this.callOffChainMethodAsync(AbiItemType.getMarketInfo, []);
    const e = this.objectFromEntries(result) as ContractMarketInfo;
    logger.info("getMarketInfoAsync", e)();
    return {
      marketStarted: this.toBoolean(e.market_started),
      mintsRemaining: parseInt(e.mints_remaining),
      mintPrice: TransformUtil.toBigNumber(e.mint_price),
      actionPrice: TransformUtil.toBigNumber(e.action_price)
    };
  }

  async setMarketStartedAsync(_account: WalletAccount, _started: boolean): Promise<any> {
    logger.info("setMarketStartedAsync", _started)();
    return this.callContractAsync(_account, 'setMarketStarted', [_started]);
  }

  async isContractAsync(_address: string): Promise<boolean> {
    const result = await this.callOffChainMethodAsync(AbiItemType.isContract, [_address]);
    if (!result) {
      return false;
    }
    const value = result[0].value;
    logger.info("isContractAsync", value)();
    return this.toBoolean(value);
  }

  async balanceOfAsync(_owner: string): Promise<any> {
    const result = await this.callOffChainMethodAsync(AbiItemType.balanceOf, [_owner]);
    if (!result) {
      return undefined;
    }
    const value = result[0].value;
    logger.info("balanceOfAsync", value)();
    return value;
  }

  async ownerOfAsync(_tokenId: string): Promise<any> {
    const result = await this.callOffChainMethodAsync(AbiItemType.ownerOf, [_tokenId]);
    if (!result) {
      return undefined;
    }
    const value = result[0].value;
    logger.info("ownerOfAsync", value)();
    return value;
  }

  async getMappedIndexAsync(_refId: string): Promise<Maybe<BigNumber>> {
    const result = await this.callOffChainMethodAsync(AbiItemType.getMappedIndex, [_refId]);
    if (!result) {
      return undefined;
    }
    const value = result[0].value;
    logger.info("getMappedIndexAsync", _refId, ":", value)();
    return TransformUtil.toBigNumber(value);
  }

  async setMappedIndicesAsync(_account: WalletAccount, _indices: string[], _startIndex: string): Promise<any> {
    return this.callContractAsync(_account, 'setMappedIndices', [_indices, _startIndex]);
  }

  async mintAsync(_account: WalletAccount, _mintPrice: string): Promise<any> {
    return this.callContractAsync(_account, 'mint', [], _mintPrice);
  }

  async totalSupplyAsync(_owner: string): Promise<any> {
    const result = await this.callOffChainMethodAsync(AbiItemType.totalSupply, []);
    if (!result) {
      return undefined;
    }
    const value = result[0].value;
    logger.info("totalSupplyAsync", value)();
    return value;
  }

  async getTokensOfAsync(_owner: string): Promise<string[]> {
    const result = await this.callOffChainMethodAsync(AbiItemType.getTokensOf, [_owner]);
    if (!result) {
      return [];
    }
    const value = result[0].value;
    logger.info("getTokensOfAsync", value)();
    return value;
  }

  async tokenURIAsync(_tokenId: string): Promise<any> {
    const result = await this.callOffChainMethodAsync(AbiItemType.tokenURI, [_tokenId]);
    if (!result) {
      return undefined;
    }
    const value = result[0].value;
    logger.info("tokenURIAsync", value)();
    return value;
  }

  async getBaseUrlAsync(): Promise<any> {
    const result = await this.callOffChainMethodAsync(AbiItemType.getBaseUrl, []);
    if (!result) {
      return undefined;
    }
    const value = result[0].value;
    logger.info("getBaseUrlAsync", value)();
    return value;
  }

  async setBaseUrlAsync(_account: WalletAccount, _baseUrl: string): Promise<any> {
    return this.callContractAsync(_account, 'setBaseUrl', [_baseUrl]);
  }

  async getOfferAsync(_punkId: string): Promise<ContractMarketOffer> {
    const result = await this.callOffChainMethodAsync(AbiItemType.getOffer, [_punkId]);
    logger.info("getOfferAsync", "_punkId", _punkId, result)();
    return {
      isForSale: this.toBoolean(result[0].value),
      itemIndex: new BigNumber(_punkId),
      seller: result[1].value,
      minAmount: new BigNumber(result[2].value),
      onlySellTo: TransformUtil.toAddress(result[3].value)
    };
  }

  async getBidAsync(_punkId: string): Promise<ContractMarketBid> {
    const result = await this.callOffChainMethodAsync(AbiItemType.getBid, [_punkId]);
    logger.info("getBidAsync", "_punkId", _punkId, result)();
    return {
      hasBid: this.toBoolean(result[0].value),
      itemIndex: new BigNumber(_punkId),
      bidder: result[1].value,
      amount: new BigNumber(result[2].value)
    };
  }

  async cancelOfferAsync(_account: WalletAccount, _punkId: string): Promise<any> {
    return this.callContractAsync(_account, 'cancelOffer', [_punkId]);
  }

  async offerAsync(_account: WalletAccount, _punkId: string, _minSalePrice: string, _fee?: Maybe<string>): Promise<any> {
    logger.info(`offerAsync punkId: ${_punkId} minSalePrice: ${_minSalePrice} fee: ${_fee}`)();
    return this.callContractAsync(_account, 'offer', [_punkId, _minSalePrice], _fee ?? undefined);
  }

  async offerToAddressAsync(_account: WalletAccount, _punkId: string, _minSalePrice: string, _address: string, _fee?: Maybe<string>): Promise<any> {
    logger.info(`offerToAddressAsync punkId: ${_punkId} minSalePrice: ${_minSalePrice} address: ${_address} fee: ${_fee}`)();
    return this.callContractAsync(_account, 'offerToAddress', [_punkId, _minSalePrice, _address], _fee ?? undefined);
  }

  async buyAsync(_account: WalletAccount, _punkId: string, _amount: string): Promise<any> {
    return this.callContractAsync(_account, 'buy', [_punkId], _amount);
  }

  async cancelBidAsync(_account: WalletAccount, _punkId: string): Promise<any> {
    return this.callContractAsync(_account, 'cancelBid', [_punkId]);
  }

  async enterBidAsync(_account: WalletAccount, _punkId: string, _amount: string, _fee?: Maybe<string>): Promise<any> {
    logger.info(`enterBidAsync punkId: ${_punkId} amount: ${_amount} fee: ${_fee}`)();
    let amount = _amount;
    if (_fee && !CommonUtil.isNullOrWhitespace(_fee)) {
      amount = new BigNumber(_amount).plus(new BigNumber(_fee)).toFixed();
      logger.info(`enterBidAsync amount+fee: ${amount}`)();
    }
    return this.callContractAsync(_account, 'enterBid', [_punkId], amount);
  }

  async acceptBidAsync(_account: WalletAccount, _punkId: string, _minPrice: string): Promise<any> {
    return this.callContractAsync(_account, 'acceptBid', [_punkId, _minPrice]);
  }
}

const contract = new PunksContract();

export const getPunksContract = () => {
  return contract;
}