import { ApolloClient, FetchPolicy, NormalizedCacheObject } from "@apollo/client";
import { getApolloClient } from "../clients/apollo.client";
import { CommonConfig } from "../common/config";
import { GET_QUOTA_QUERY } from "../queries/general";
import { GetQuota, GetQuotaVariables } from "../queries/__generated__/GetQuota";

class GeneralService {
  private readonly _apollo: ApolloClient<NormalizedCacheObject>;

  constructor() {
    this._apollo = getApolloClient();
  }

  getContractQuota = (_fetchPolicy: FetchPolicy = "network-only") => {
    return this._apollo.query<GetQuota, GetQuotaVariables>({
      query: GET_QUOTA_QUERY,
      variables: {
        address: CommonConfig.CONTRACT_ADDRESS
      },
      fetchPolicy: _fetchPolicy
    })
  }
}

const service = new GeneralService();

export const getGeneralService = () => {
  return service;
}