import { Typography } from '@mui/material';
import React from 'react';
import { MomentUtil } from '../../../../util/moment.util';

interface Props {
  date: string
}

type CountdownState = {
  days: number
  hours: number
  minutes: number
  seconds: number
}

export const Countdown: React.FC<Props> = (props: Props) => {
  const moment = React.useMemo<MomentUtil>(() => {
    return new MomentUtil();
  }, []);
  const duration = moment.getDuration(props.date);
  const mounted = React.useRef(false);
  const [state, setState] = React.useState<CountdownState>({
    days: duration.days() > 0 ? duration.days() : 0,
    hours: duration.hours() > 0 ? duration.hours() : 0,
    minutes: duration.minutes() > 0 ? duration.minutes() : 0,
    seconds: duration.seconds() > 0 ? duration.seconds() : 0
  });

  if (duration.asSeconds() > 0) {
    setTimeout(() => {
      const duration = moment.getDuration(props.date);
      if (mounted.current) {
        setState({
          days: duration.days(),
          hours: duration.hours(),
          minutes: duration.minutes(),
          seconds: duration.seconds()
        });
      }
    }, 500);
  }

  React.useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  return (
    <Typography variant="subtitle1" color="textSecondary">
      <b>{state.days}</b> days <b>{state.hours}</b> hours <b>{state.minutes}</b> minutes <b>{state.seconds}</b> seconds
    </Typography>
  );
}
