import BigNumber from "bignumber.js";
import { ViteClient } from "../clients/vite.client";
import { FileUtil, getFileUtil } from "../util/file.util";
import { FormatUtil } from "../util/format.util";
import { getLogger } from "../util/logger";
import { Contract, ViteSubscriptionResult } from "../util/types";
import { WalletAccount } from "../wallet";

const logger = getLogger();

export abstract class BaseContract {
  protected readonly _fileUtil: FileUtil;
  private readonly _minQuota: BigNumber = new BigNumber("10000");
  protected _client!: ViteClient;
  protected _contract!: Contract;

  constructor() {
    this._fileUtil = getFileUtil();
  }

  async initAsync(client: ViteClient, basePath: string): Promise<void> {
    this._client = client;
    await this.initAsyncProtected(basePath);
  }

  protected abstract initAsyncProtected(basePath: string): Promise<void>;

  get contract(): Contract {
    return this._contract;
  }

  protected async ensureQuotaAsync(_quota: BigNumber = this._minQuota): Promise<void> {
    const result = await this._client.getQuotaAsync(this._contract.address);
    if (result.currentQuota.lte(_quota)) {
      throw new Error("Contract has insufficient quota");
    }
  }

  protected toBoolean(value: string): boolean {
    if (value === "0") {
      return false;
    } else if (value === "1") {
      return true;
    } else {
      throw new Error(`Value ${value} is not boolean`)
    }
  }

  protected objectFromEntries = (entries: any) => {
    return Object.fromEntries(
      entries.map((entry: any) => {
        return [entry.name, entry.value];
      })
    );
  }

  protected async callContractAsync(_account: WalletAccount, _methodName: string, _params: any[], amount?: string, tokenId?: string): Promise<any> {
    await this.ensureQuotaAsync();
    try {
      const result1 = await this._client.callContractAsync(_account, _methodName, this._contract.abi, _params, this._contract.address, amount, tokenId);
      const result2 = await this._client.waitForAccountBlockAsync(_account.address, result1.height);
      return result2;
    } catch (error: any) {
      const message = FormatUtil.formatMessage(error);
      if (message) {
        throw new Error(message)
      } else {
        throw new Error("Error");
      }
    }
  }

  async stakeForQuotaAsync(_account: WalletAccount, _amount: string): Promise<any> {
    const result1 = await this._client.stakeForQuotaAsync(_account, this._contract.address, _amount);
    const result2 = await this._client.waitForAccountBlockAsync(_account.address, result1.height);
    return result2;
  }

  async createAddressListenerAsync(): Promise<ViteSubscriptionResult> {
    const logs: any[] = [];
    const listener = await this._client.createAddressListenerAsync(this._contract.address);
    listener.on((results: any[]) => {
      for (let index = 0; index < results.length; index++) {
        const result = results[index];
        logger.info(result)();
        const vmLog = this._client.decodeVmLog(result.vmlog, this._contract.abi);
        logs.push(vmLog);
      }
    });

    return {
      logs,
      listener
    }
  }
}