import { FetchPolicy, useQuery } from "@apollo/client";
import React from "react";
import { GET_MARKET_BID_QUERY, GET_MARKET_OFFER_QUERY, GET_PUNK_OWNER_QUERY, GET_PUNK_QUERY } from "../queries";
import { GetMarketBid, GetMarketBidVariables } from "../queries/__generated__/GetMarketBid";
import { GetMarketOffer, GetMarketOfferVariables } from "../queries/__generated__/GetMarketOffer";
import { GetPunk, GetPunkVariables } from "../queries/__generated__/GetPunk";
import { GetPunkOwner, GetPunkOwnerVariables } from "../queries/__generated__/GetPunkOwner";
import { getPunkService } from "../services/punk.service";
import { getLogger } from "../util/logger";
import { Punk, TypeNames } from "../util/types";

const logger = getLogger();

export const usePunkById = (_punkId: string, _fetchPolicy: FetchPolicy = "network-only") => {
  const query = useQuery<GetPunk, GetPunkVariables>(GET_PUNK_QUERY, {
    variables: {
      id: _punkId
    },
    notifyOnNetworkStatusChange: true,
    fetchPolicy: _fetchPolicy,
    refetchWritePolicy: "overwrite"
  });

  const error = query.error;
  const loading = query.loading;

  const punk: Maybe<Punk> = query.data?.punk

  return {
    punk,
    error,
    loading,
  };
}

export const usePunkInfo = (_punk: Punk, _fetchPolicy: FetchPolicy = "network-only") => {
  const defaultPunkExtended = React.useMemo<Punk>(() => {
    return {
      __typename: TypeNames.Punk,
      id: _punk.id,
      rank: _punk.rank,
      image: _punk.image,
      attributes: _punk.attributes
    };
  }, [_punk]);
  const [punkExtended, setPunkExtended] = React.useState<Punk>(defaultPunkExtended);
  const [dateRef, setDateRef] = React.useState<Date>(new Date(0));

  const ownerQuery = useQuery<GetPunkOwner, GetPunkOwnerVariables>(GET_PUNK_OWNER_QUERY, {
    variables: {
      id: _punk.id.toString()
    },
    notifyOnNetworkStatusChange: true,
    fetchPolicy: _fetchPolicy,
    refetchWritePolicy: "overwrite",
    onCompleted: (data) => {
      setPunkExtended({
        ...punkExtended,
        owner: data.punkOwner
      });
    }
  });

  const bidQuery = useQuery<GetMarketBid, GetMarketBidVariables>(GET_MARKET_BID_QUERY, {
    variables: {
      id: _punk.id.toString()
    },
    notifyOnNetworkStatusChange: true,
    fetchPolicy: _fetchPolicy,
    refetchWritePolicy: "overwrite",
    onCompleted: (data) => {
      setPunkExtended({
        ...punkExtended,
        bid: data.marketBid
      });
    }
  });

  const offerQuery = useQuery<GetMarketOffer, GetMarketOfferVariables>(GET_MARKET_OFFER_QUERY, {
    variables: {
      id: _punk.id.toString()
    },
    notifyOnNetworkStatusChange: true,
    fetchPolicy: _fetchPolicy,
    refetchWritePolicy: "overwrite",
    onCompleted: (data) => {
      setPunkExtended({
        ...punkExtended,
        offer: data.marketOffer
      });
    }
  });

  const error = ownerQuery.error || bidQuery.error || offerQuery.error;
  const loading = ownerQuery.loading || bidQuery.loading || offerQuery.loading;

  const refetchAsync = async () => {
    logger.info("refetchAsync", new Date().getTime(), dateRef.getTime())();
    if (new Date().getTime() >= dateRef.getTime() + 500) {
      setDateRef(new Date());
      let newOwner;
      let newBid;
      let newOffer;
      try {
        await Promise.all([
          ownerQuery.refetch().then(e => newOwner = e.data.punkOwner),
          bidQuery.refetch().then(e => newBid = e.data.marketBid),
          offerQuery.refetch().then(e => newOffer = e.data.marketOffer)
        ]);
        logger.info(`Refetching punk ${_punk.id}`, newOwner, newBid, newOffer)();
        setPunkExtended({
          ...defaultPunkExtended,
          owner: newOwner,
          bid: newBid,
          offer: newOffer
        });
      } catch (error) {
        logger.error(error)();
      }
    }
  }

  return {
    punkExtended,
    error,
    loading,
    refetchAsync
  };
}

export const usePunkInfoV2 = (_punk: Punk, _fetchPolicy: FetchPolicy = "network-only") => {
  const service = getPunkService();
  const defaultPunkExtended = React.useMemo<Punk>(() => {
    return {
      __typename: TypeNames.Punk,
      id: _punk.id,
      rank: _punk.rank,
      image: _punk.image,
      attributes: _punk.attributes
    };
  }, [_punk]);
  const [punkExtended, setPunkExtended] = React.useState<Punk>(defaultPunkExtended);
  const [loading, setLoading] = React.useState<boolean>(true);
  const [error, setError] = React.useState<Maybe<any>>(undefined);

  const refetchAsync = async () => {
    setLoading(true);
    setError(undefined);
    try {
      const { punkExtended, loading, error } = await service.getExtendedAsync(_punk, _fetchPolicy);
      setPunkExtended(punkExtended);
      setLoading(loading);
      setError(error);
    } catch (error) {
      setLoading(false);
      setError(error);
    }
  }

  return {
    punkExtended,
    loading,
    error,
    refetchAsync
  }
}

