import BigNumber from "bignumber.js";
import { orderBy } from "lodash";
import { OrderBy, PunkSortBy } from "../__generated__/globalTypes";
import { Punk } from "./types";

export abstract class FilterUtil {
  static sortPunks(sort: PunkSortBy, order: OrderBy, punks?: Maybe<Punk[]>): Maybe<Punk[]> {
    if (!punks || !sort || !order) {
      return punks;
    }
    switch (sort) {
      case PunkSortBy.ID:
        return orderBy(
          punks,
          (p: Punk) => Number(p.id),
          order === OrderBy.ASC ? 'asc' : 'desc',
        )
      case PunkSortBy.RANK:
        return orderBy(
          punks,
          (p: Punk) => p.rank,
          order === OrderBy.ASC ? 'asc' : 'desc',
        )
      case PunkSortBy.BID:
        return punks.filter(e => !!e.bid && e.bid.amount.gt(0)).sort((a: Punk, b: Punk) => {
          const _a: BigNumber = a.bid?.amount ?? new BigNumber(0);
          const _b: BigNumber = b.bid?.amount ?? new BigNumber(0);
          if (order === OrderBy.ASC) {
            return _a.gt(_b) ? 1 : -1;
          }
          return _a.gt(_b) ? -1 : 1;
        })
      case PunkSortBy.OFFER:
        return punks.filter(e => !!e.offer && e.offer.minAmount.gt(0)).sort((a: Punk, b: Punk) => {
          const _a: BigNumber = a.offer?.minAmount ?? new BigNumber(0);
          const _b: BigNumber = b.offer?.minAmount ?? new BigNumber(0);
          if (order === OrderBy.ASC) {
            return _a.gt(_b) ? 1 : -1;
          }
          return _a.gt(_b) ? -1 : 1;
        })
      default:
        return punks;
    }
  }
}