import { BigNumber } from "bignumber.js";
import { CommonConstants, DefaultPunkFilterValues } from "../common/constants";
import { getPunkSearch, PunkSearch } from "../search";
import { CommonUtil } from "../util/common.util";
import { CompressUtil } from "../util/compress.util";
import { getEmitter, IGlobalEmitter } from "../util/emitter";
import { Ensure } from "../util/ensure";
import { FileUtil, getFileUtil } from "../util/file.util";
import { FilterUtil } from "../util/filter.util";
import { getLogger } from "../util/logger";
import { MomentUtil } from "../util/moment.util";
import { FilteredCollection, MarketBid, MarketInfo, MarketOffer, Network, Punk, PunkEvents, PunkInfo, TypeNames, ViteQuotaResult } from "../util/types";
import { getWalletManager, WalletAccount, WalletManager } from "../wallet";
import { OrderBy, PunkSortBy } from "../__generated__/globalTypes";

const logger = getLogger();

export interface IDataSource {
  initBaseAsync(network: Network): Promise<void>;
  initAsync(network: Network): Promise<void>;
  dispose(): void;
  getQuotaAsync(_address: string): Promise<ViteQuotaResult>;
  getPunkAttributesAsync(): Promise<string[]>;
  getPunkEventsAsync(_id: string, _limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkEvents>;
  getPunkOwnerAsync(_id: string): Promise<Maybe<string>>;
  getPunkAsync(_id: string): Promise<Punk>;
  getPunksAsync(_limit: number, _offset: number, _sort: string, _order: string, _search: string): Promise<Punk[]>;
  getPunksAllAsync(): Promise<Punk[]>;
  getPunksByAddressAsync(_address: string, _limit: number, _offset: number): Promise<Punk[]>;
  getMarketBidAsync(_id: string): Promise<Maybe<MarketBid>>;
  getMarketOfferAsync(_id: string): Promise<Maybe<MarketOffer>>;
  getMarketEventsAsync(_limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkEvents>;
  getMarketInfoAsync(): Promise<MarketInfo>;
  getActionPriceAsync(): Promise<BigNumber>;
  getMintPriceAsync(): Promise<BigNumber>;
  mintAsync(): Promise<boolean>;
  offerAsync(_id: string, _amount: string, _address?: Maybe<string>): Promise<boolean>;
  cancelOfferAsync(_id: string): Promise<boolean>;
  buyAsync(_id: string, _amount: string): Promise<boolean>;
  bidAsync(_id: string, _amount: string): Promise<boolean>;
  acceptBidAsync(_id: string, _amount: string): Promise<boolean>;
  cancelBidAsync(_id: string): Promise<boolean>;
}

export abstract class BaseDataSource implements IDataSource {
  protected _punkInfos?: PunkInfo[];
  protected readonly _emitter: IGlobalEmitter;
  protected _moment: MomentUtil = new MomentUtil();
  protected readonly _fileUtil: FileUtil;
  protected _actionPrice?: BigNumber;
  protected _mintPrice?: BigNumber;
  private readonly _walletManager: WalletManager;
  private readonly _punkSearch: PunkSearch;
  private _punkAttributes!: string[];
  private _punks!: Punk[];
  private _punksFiltered!: FilteredCollection<Punk>;

  constructor() {
    this._emitter = getEmitter();
    this._fileUtil = getFileUtil();
    this._walletManager = getWalletManager();
    this._punkSearch = getPunkSearch();
    this.clear();
  }

  private clear(): void {
    this._punkAttributes = [];
    this._punks = [];
    this._punksFiltered = {
      sortBy: DefaultPunkFilterValues.sortBy,
      orderBy: DefaultPunkFilterValues.orderBy,
      searchTerm: DefaultPunkFilterValues.search,
      items: []
    }
  }

  private async initPunkAttributesAsync(): Promise<void> {
    let attributes = await this._fileUtil.readFileAsync("./assets/data/punk_attributes.json");
    attributes = JSON.parse(attributes);
    this._punkAttributes = attributes;
  }

  private async initPunksAsync(): Promise<void> {
    let punks = await this._fileUtil.readFileAsync("./assets/data/punks.gz", "binary");
    punks = JSON.parse(CompressUtil.decompress(CommonUtil.toUint8Array(punks)));
    let infos = this._punkInfos;
    if (!infos || infos.length !== CommonConstants.TOTAL_MINTS) {
      logger.info("Invalid punk infos")();
      infos = this.getDefaultPunkInfos();
    }
    for (let index = 0; index < punks.length; index++) {
      const p = punks[index];
      const info = infos[index];
      const punk: Punk = {
        __typename: TypeNames.Punk,
        id: p.index.toString(),
        rank: p.rank,
        image: p.image,
        attributes: p.attributes,
        owner: info.owner ?? null,
        bid: info.bid ?? null,
        offer: info.offer ?? null
      };
      this._punks.push(punk);
      if (this._punksFiltered.orderBy === OrderBy.ASC) {
        this._punksFiltered.items.push(punk);
      } else {
        this._punksFiltered.items.unshift(punk);
      }
    }
  }

  async initBaseAsync(network: Network): Promise<void> {
    logger.info("Init BaseDataSource")();
    this._moment = new MomentUtil();
    this.clear();
    await this.initAsync(network);
    await this.initPunkAttributesAsync();
    await this.initPunkInfosAsync();
    await this.initPunksAsync();
    this._punkSearch.index(this._punks);
  }

  dispose(): void {
    logger.info("Disposing BaseDataSource")();
    this.disposeProtected();
  }

  getAccount(): WalletAccount {
    const account = this._walletManager.getActiveAccount();
    Ensure.notNull(account, "account", "Please connect your wallet first.");
    return account as WalletAccount;
  }

  async getPunkAttributesAsync(): Promise<string[]> {
    return this._punkAttributes;
  }

  async getPunkAsync(id: string): Promise<Punk> {
    if (!CommonUtil.isNumber(id)) {
      throw new Error("Invalid punk identifier.");
    }
    const punkId = parseInt(id) - 1;
    if (punkId < 0 || punkId >= this._punks.length) {
      throw new Error("Punk does not exist.");
    }
    return this._punks[punkId];
  }

  async getPunksAsync(_limit: number, _offset: number, _sort: string, _order: string, _search: string): Promise<Punk[]> {
    // check if sort, order or search changed -> update punksFiltered
    if (this._punksFiltered.searchTerm !== _search || this._punksFiltered.sortBy !== _sort || this._punksFiltered.orderBy !== _order) {
      logger.info("limit", _limit, "offset", _offset, "sort", _sort, "order", _order)();
      this._punksFiltered.searchTerm = _search;
      this._punksFiltered.items = this._punkSearch.search(_search, this._punks) ?? [];
      this._punksFiltered.sortBy = _sort;
      this._punksFiltered.orderBy = _order;
      this._punksFiltered.items = FilterUtil.sortPunks(this._punksFiltered.sortBy as PunkSortBy, this._punksFiltered.orderBy as OrderBy, this._punksFiltered.items) ?? [];
    }
    return this._punksFiltered.items.slice(_offset, _offset + _limit);
  }

  async getPunksAllAsync(): Promise<Punk[]> {
    return this._punks;
  }

  async getActionPriceAsync(): Promise<BigNumber> {
    if (!this._actionPrice) {
      const result = await this.getMarketInfoAsync();
      this._actionPrice = result.actionPrice;
    }
    return this._actionPrice;
  }

  async getMintPriceAsync(): Promise<BigNumber> {
    if (!this._mintPrice) {
      const result = await this.getMarketInfoAsync();
      this._mintPrice = result.mintPrice;
    }
    return this._mintPrice;
  }

  protected createId(): string {
    return CommonUtil.uuid();
  }

  protected getDefaultPunkInfos(): PunkInfo[] {
    const infos: PunkInfo[] = [];
    for (let index = 1; index <= CommonConstants.TOTAL_MINTS; index++) {
      infos.push({
        id: index.toString()
      })
    }
    return infos;
  }

  abstract initAsync(network: Network): Promise<void>;
  abstract initPunkInfosAsync(): Promise<void>;
  protected abstract disposeProtected(): void;
  abstract getQuotaAsync(_address: string): Promise<ViteQuotaResult>;
  abstract getPunkEventsAsync(_id: string, _limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkEvents>;
  abstract getPunkOwnerAsync(_id: string): Promise<Maybe<string>>;
  abstract getPunksByAddressAsync(_address: string, _limit: number, _offset: number): Promise<Punk[]>;
  abstract getMarketBidAsync(_id: string): Promise<Maybe<MarketBid>>;
  abstract getMarketOfferAsync(_id: string): Promise<Maybe<MarketOffer>>;
  abstract getMarketEventsAsync(_limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkEvents>;
  abstract getMarketInfoAsync(): Promise<MarketInfo>;
  abstract mintAsync(): Promise<boolean>;
  abstract offerAsync(_id: string, _amount: string, _address?: Maybe<string>): Promise<boolean>;
  abstract cancelOfferAsync(_id: string): Promise<boolean>;
  abstract buyAsync(_id: string, _amount: string): Promise<boolean>;
  abstract bidAsync(_id: string, _amount: string): Promise<boolean>;
  abstract acceptBidAsync(_id: string, _amount: string): Promise<boolean>;
  abstract cancelBidAsync(_id: string): Promise<boolean>;
}