import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Link } from '@mui/material';
import confetti from 'canvas-confetti';
import React, { useEffect, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { CommonConstants } from '../../../../common/constants';
import { useConnectedWeb3Context } from '../../../../contexts/connectedWeb3';
import { getPunkService } from '../../../../services/punk.service';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { GlobalEvent, PunkChangedEvent, PunkEventType } from '../../../../util/types';
import { PunkImage } from '../image';

const logger = getLogger();

export const MintDialog: React.FC = () => {
  const [open, setOpen] = useState(false);
  const [punkId, setPunkId] = useState(-1);
  const [punkImage, setPunkImage] = useState("");
  const emitter = getEmitter();
  const service = getPunkService();
  const context = useConnectedWeb3Context();

  useEffect(() => {
    const handleEvent = async (e: PunkChangedEvent) => {
      //logger.info(`eventType: ${e.eventType} punkId: ${e.punkId} amount: ${e.amount?.toString()} fromAddress: ${e.fromAddress} toAddress: ${e.toAddress} timestamp: ${e.timestamp.toString()}`)();
      if (e.eventType === PunkEventType.Mint && e.toAddress?.toLowerCase() === context.account?.toLowerCase()) {
        logger.info("Show mint dialog")();
        const punk = await service.getById(e.punkId.toString());
        setPunkId(e.punkId);
        setPunkImage(punk.data.punk.image);
        setOpen(true);
      }
    }
    emitter.on(GlobalEvent.PunkChanged, handleEvent)
    return () => {
      emitter.off(GlobalEvent.PunkChanged, handleEvent)
    };
  }, [emitter, service, context.account, setOpen]);

  useEffect(() => {
    if (open) {
      const triggerConfetti = () => {
        confetti({
          particleCount: 200,
          spread: 100,
          zIndex: 9999
        });
      }
      for (let index = 1; index < 4; index++) {
        setTimeout(() => {
          triggerConfetti();
        }, index * 225);
      }
    }
  }, [open]);

  const handleClose = () => {
    setPunkImage("");
    setOpen(false);
  };

  if (punkId < 0) {
    return (
      <>
      </>
    )
  }

  return (
    <Dialog
      open={open}
      transitionDuration={500}
      keepMounted
      onClose={handleClose}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle> Mint success!</DialogTitle>
      <DialogContent>
        <DialogContentText paddingBottom={1}>
          You have successfully minted <Link underline="hover" component={RouterLink} to={`/punk/${punkId}`} onClick={handleClose}>{CommonConstants.PUNK_NAME} #{punkId}</Link>
        </DialogContentText>
        <PunkImage image={punkImage} maxHeight="300px"></PunkImage>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>
          Close
        </Button>
        <Button component={RouterLink} to={`/account/${context.account}`} variant="contained" onClick={handleClose}>
          My punks
        </Button>
      </DialogActions>
    </Dialog>
  )
}
