import { ApolloClient, InMemoryCache, NormalizedCacheObject } from '@apollo/client';
import { offsetLimitPagination } from '@apollo/client/utilities';
import BigNumber from 'bignumber.js';
import { GraphQLScalarType } from 'graphql';
import { getCommonContext } from '../contexts/common.context';
import { IDataSource } from '../datasources';
import { MarketMutations, PunkMutations } from '../mutations';
import { MarketQueries, PunkQueries } from '../queries';
import { GeneralQueries } from '../queries/general';

export type ApolloContext = {
  cache: InMemoryCache,
  client: ApolloClientWrapper,
  clientAwareness: any,
  getCacheKey: (obj: any) => string | undefined
}

export class ApolloClientWrapper extends ApolloClient<NormalizedCacheObject> {
  get datasource(): IDataSource {
    return getCommonContext().datasource;
  }
}

const cache: InMemoryCache = new InMemoryCache({
  typePolicies: {
    Query: {
      fields: {
        // https://www.apollographql.com/docs/react/pagination/offset-based/
        punks: offsetLimitPagination(["order", "sort"]),
        punksByAddress: offsetLimitPagination(["address"]),
        //marketEvents: offsetLimitPagination()
      }
    }
  }
});

const bigNumberScalar = new GraphQLScalarType({
  name: 'BigNumber',
  description: 'BigNumber custom scalar type',
  serialize(value: BigNumber) {
    return value.toFixed();
  },
  parseValue(value: string) {
    return new BigNumber(value);
  }
});

const resolvers: any = {
  BigNumber: bigNumberScalar,
  Query: {
    ...GeneralQueries,
    ...PunkQueries,
    ...MarketQueries
  },
  Mutation: {
    ...PunkMutations,
    ...MarketMutations
  }
};

// https://www.apollographql.com/docs/react/networking/authentication/#header
const client = new ApolloClientWrapper({
  // link: authLink.concat(httpLink),
  cache: cache,
  resolvers,
  headers: {
    'client-name': process.env.REACT_APP_NAME || 'client',
    'client-version': process.env.REACT_APP_VERSION || '0',
  },
})

export const getApolloClient = () => {
  return client;
}