import { gql } from "@apollo/client";
import { ApolloContext } from "../clients/apollo.client";

export const MINT_MUTATION = gql`
  mutation Mint {
    mint @client
  }
`;

export const PunkMutations = {
  async mint(parent: any, params: any, context: ApolloContext): Promise<boolean> {
    return context.client.datasource.mintAsync();
  },
}