import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import { Accordion as MuiAccordion, AccordionDetails as MuiAccordionDetails, AccordionProps, AccordionSummary as MuiAccordionSummary, AccordionSummaryProps, Box, Link, styled, Typography } from '@mui/material';
import React from 'react';

const Accordion = styled((props: AccordionProps) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)': {
    borderBottom: 0,
  },
  '&:before': {
    display: 'none',
  },
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(0, 0, 0, .2)'
      : 'rgba(255, 255, 255, .2)',
}));

const AccordionSummary = styled((props: AccordionSummaryProps) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(255, 255, 255, .05)'
      : 'rgba(0, 0, 0, .03)',
  flexDirection: 'row-reverse',
  '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
    transform: 'rotate(90deg)',
  },
  '& .MuiAccordionSummary-content': {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

export const LandingFaq = () => {
  const [faqExpanded, setFaqExpanded] = React.useState<string | false>('faq0');

  const handleChange = (panel: string) => (event: React.SyntheticEvent, newExpanded: boolean) => {
    setFaqExpanded(newExpanded ? panel : false);
  };

  return (
    <>
      <Box display="flex" justifyContent="center" alignItems="center" marginBottom={2}>
        <div>
          <Typography variant="h5" color="textSecondary" textAlign="center">
            FAQ
          </Typography>
          <Typography variant="subtitle1" color="textSecondary" textAlign="center">
            Frequently Asked Questions
          </Typography>
        </div>
      </Box>
      <Box display="flex" justifyContent="center" alignItems="center">
        <Box maxWidth={800}>
          <Accordion expanded={faqExpanded === 'faq0'} onChange={handleChange('faq0')}>
            <AccordionSummary aria-controls="faq0-content" id="faq0-header">
              <Typography>What is the vision behind VitePunks?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Our main vision is to develop the best possible marketplace for our punks and maybe open it to other artists at some point, depending on what other NFT marketplaces will be released on Vite in the near future.
                What is very important to us is long-term availability. We would like to guarantee that our punks will be accessible as long as Vite exists.
                For example we plan to store our images on IPFS in addition to our own cloud storage.
                <br></br>
                <br></br>
                The compiled version of this website can already be
                cloned and forked on <Link underline="hover" href="https://github.com/vitepunks/vitepunks.github.io" target="_blank">GitHub</Link> or
                accessed by this link <Link underline="hover" href="https://vitepunks.github.io/website/" target="_blank">https://vitepunks.github.io/website/</Link>.
                Always with the hope to ensure resiliency and establish more trust overall long-term.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion expanded={faqExpanded === 'faq1'} onChange={handleChange('faq1')}>
            <AccordionSummary aria-controls="faq1-content" id="faq1-header">
              <Typography>Why is the smart contract not open-source yet?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                We understand that the situation for some is not satisfactory right now. Being pioneers in any space is always coupled with a lot of work, sometimes it gets rewarded, more often not.
                What we can assure is that we will make our smart contract public as soon as another high-quality project delivers something similar to us.
                Maybe this is the wrong approach but ultimately this is actually a really hard decision to make.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion expanded={faqExpanded === 'faq2'} onChange={handleChange('faq2')}>
            <AccordionSummary aria-controls="faq2-content" id="faq2-header">
              <Typography>Where can I download the Vite Wallet App?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                The Vite App Wallet can be downloaded for <Link underline="hover" href="https://play.google.com/store/apps/details?id=net.vite.wallet" target="_blank">Android</Link>
                &nbsp;or <Link underline="hover" href="https://apps.apple.com/ch/app/vite-multi-chain-wallet/id1437629486" target="_blank">iOS</Link>.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion expanded={faqExpanded === 'faq3'} onChange={handleChange('faq3')}>
            <AccordionSummary aria-controls="faq3-content" id="faq3-header">
              <Typography>How can I connect my wallet on my smartphone?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Connecting the wallet on your smartphone can be tricky because it is not possible to scan the QR code currently being displayed on your screen.
                To overcome this limitation detailed instructions can be found in our <Link underline="hover" href="https://github.com/vitepunks/resources/wiki/Connect-Wallet" target="_blank">wiki article</Link>.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion expanded={faqExpanded === 'faq4'} onChange={handleChange('faq4')}>
            <AccordionSummary aria-controls="faq4-content" id="faq4-header">
              <Typography>Is it possible to view my minted punks in my wallet?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Before this can be achieved, the Vite community has to agree on a commonly accepted standard.
                Currently it is being worked on as a bounty started by ViteLabs. You can find it <Link underline="hover" href="https://gitcoin.co/issue/vitelabs/bounties/18/100027282" target="_blank">here</Link>.
                So in this sense we are truly similar to the original CryptoPunks project which pioneered the NFT space without having a standard at hand when released.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion expanded={faqExpanded === 'faq5'} onChange={handleChange('faq5')}>
            <AccordionSummary aria-controls="faq5-content" id="faq5-header">
              <Typography>How can I gift/transfer my punk to someone else?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                This can be achieved by offering your punk for sale for 0 VITE to a specific address. Only this address will be able to claim the punk.
                Just follow the steps described in this <Link underline="hover" href="https://github.com/vitepunks/resources/wiki/Gifting" target="_blank">wiki article</Link>.
              </Typography>
            </AccordionDetails>
          </Accordion>
        </Box>
      </Box>
      <Typography variant="subtitle1" color="textSecondary" textAlign="center" marginTop={2}>
        Community articles
      </Typography>
      <Typography variant="subtitle2" color="textSecondary" textAlign="center">
        <Link underline="hover" href="https://medium.com/@CPCLinoCrypto/cuestionario-realizado-a-vitepunks-dd6f3e40dae8" target="_blank">[Spanish]</Link>
        &nbsp;
        <Link underline="hover" href="https://medium.com/@CPCLinoCrypto/questionnaire-answered-by-vitepunk-c1a9657c20ec" target="_blank">[English]</Link>
      </Typography>
    </>
  );
}
