import { BigNumber } from "bignumber.js";
import { CommonConstants } from "../common/constants";

export abstract class TokenUtil {
  static roundBigNumber(bn: BigNumber, tokenDecimals: number, decimals: number): BigNumber {
    return bn.div(new BigNumber("10").pow(tokenDecimals)).decimalPlaces(decimals, BigNumber.ROUND_DOWN);
  }

  static formatBigNumber(bn: BigNumber, tokenDecimals: number, decimals: number, maxDigits: number = 14): string {
    const rounded = TokenUtil.roundBigNumber(bn, tokenDecimals, decimals);
    if (rounded.lte(0)) {
      return "<1";
    }
    const formatted = rounded.toFormat();
    if (maxDigits >= formatted.length) {
      return formatted;
    }
    return formatted.substring(0, maxDigits) + "...";
  }

  static formatVite(bn: BigNumber): string {
    return TokenUtil.formatBigNumber(bn, CommonConstants.CURRENCY_DECIMALS, 2);
  }

  static formatViteTooltip(bn: BigNumber): string {
    const rounded = TokenUtil.roundBigNumber(bn, CommonConstants.CURRENCY_DECIMALS, CommonConstants.CURRENCY_DECIMALS);
    return `${rounded.toFormat()} $${CommonConstants.CURRENCY_NAME}`;
  }
}