export abstract class RetryUtil {
  // https://stackoverflow.com/questions/45444601/promise-retries-until-success-failure-with-typescript
  static async promiseRetry<T>(fn: () => Promise<T>, retries = 5, err?: any): Promise<T> {
    await new Promise(resolve => setTimeout(resolve, (5 - retries) * 1000));
    return !retries ? Promise.reject(err) : fn().catch(error => RetryUtil.promiseRetry(fn, (retries - 1), error));
  }
}