import { faCheck } from '@fortawesome/free-solid-svg-icons';
import { Button, Stack } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { CommonConstants } from '../../../../common/constants';
import { useConnectedWeb3Context } from '../../../../contexts/connectedWeb3';
import { getMarketService } from '../../../../services/market.service';
import { CommonUtil } from '../../../../util/common.util';
import { IconUtil } from '../../../../util/icon.util';
import { getLogger } from '../../../../util/logger';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { Punk, PunkDialogState, PunkDialogType, PunkEventType } from '../../../../util/types';
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { FontAwesomeSvgIcon } from '../../../common/components/fa-icon';
import { PunkDialog } from '../dialog';

const logger = getLogger();

type ShowButtonState = {
  showAny: boolean
  showBuy: boolean
  showClaim: boolean
  showBid: boolean
  showSell: boolean
  showAcceptBid: boolean
  showCancelBid: boolean
  showCancelSale: boolean
  showCancelGift: boolean
}

interface Props {
  marketStarted: boolean,
  item: Punk,
  refetchAsync: () => Promise<void>
}

export const PunkInfoActions: React.FC<Props> = (props: Props) => {
  const mounted = React.useRef(false);
  const [hasOwner, setHasOwner] = useState<boolean>(false);
  const [showAll] = useState<boolean>(false);
  const [showButtonState, setShowButtonState] = useState<ShowButtonState>({
    showAny: false,
    showBuy: false,
    showClaim: false,
    showBid: false,
    showSell: false,
    showAcceptBid: false,
    showCancelBid: false,
    showCancelSale: false,
    showCancelGift: false
  })
  const [dialogState, setDialogState] = useState<PunkDialogState>({
    type: PunkDialogType.BID,
    open: false
  });
  const context = useConnectedWeb3Context();
  const service = getMarketService();

  React.useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  useEffect(() => {
    if (mounted.current) {
      let isOwner = props.item.owner?.toLowerCase() === context.account?.toLowerCase();
      const state: ShowButtonState = {
        showAny: false,
        showBuy: false,
        showClaim: false,
        showBid: false,
        showSell: false,
        showAcceptBid: false,
        showCancelBid: false,
        showCancelSale: false,
        showCancelGift: false
      }
      // setHasOwner
      if (props.item.owner && !CommonUtil.isNullOrWhitespace(props.item.owner)) {
        setHasOwner(true);
      } else {
        setHasOwner(false);
      }
      if (props.marketStarted && !CommonUtil.isNullOrWhitespace(context.account)) {
        state.showAny = true;
      }
      if (!isOwner && props.item.offer && (CommonUtil.isNullOrWhitespace(props.item.offer.onlySellTo) || props.item.offer.onlySellTo?.toLowerCase() === context.account?.toLowerCase())) {
        if (props.item.offer.minAmount.gt(0)) {
          state.showBuy = true;
        } else {
          state.showClaim = true;
        }
      }
      if (!isOwner && props.item.bid?.bidder.toLowerCase() !== context.account?.toLowerCase()) {
        state.showBid = true;
      }
      if (isOwner && !props.item.offer) {
        state.showSell = true;
      }
      if (isOwner && props.item.bid) {
        state.showAcceptBid = true;
      }
      if (!isOwner && props.item.bid?.bidder.toLowerCase() === context.account?.toLowerCase()) {
        state.showCancelBid = true;
      }
      if (isOwner && props.item.offer) {
        if (props.item.offer.minAmount.gt(0)) {
          state.showCancelSale = true;
        } else {
          state.showCancelGift = true;
        }
      }
      setShowButtonState(state);
    }
  }, [props.marketStarted, props.item, context.account]);

  const handleFailureAsync = async () => {
    SnackbarUtil.enqueueMessage('Transaction failed');
    try {
      // Update actionPrice just in case
      await service.getMarketInfoAsync();
    } catch (error) {
      logger.error(error)();
    }
  }

  const handleBuyAsync = async (isClaim: boolean = false) => {
    try {
      if (!props.item.offer) {
        throw new Error(`${CommonConstants.PUNK_NAME} #${props.item.id} is not for sale`);
      }
      const success = await service.buyAsync(props.item.id, props.item.offer.minAmount.toFixed());
      if (success) {
        const actionName = isClaim ? "claimed" : "bought";
        SnackbarUtil.enqueueMessage(`Congratulations! You ${actionName} ${CommonConstants.PUNK_NAME} #${props.item.id}`);
      } else {
        await handleFailureAsync();
      }
    } catch (error) {
      SnackbarUtil.enqueueError(error);
    } finally {
      await props.refetchAsync();
    }
  }

  const handleAcceptBidAsync = async () => {
    try {
      if (!props.item.bid) {
        throw new Error(`${CommonConstants.PUNK_NAME} #${props.item.id} has no bid`);
      }
      const success = await service.acceptBidAsync(props.item.id, props.item.bid.amount.toFixed());
      if (success) {
        SnackbarUtil.enqueueMessage(`Congratulations! You sold ${CommonConstants.PUNK_NAME} #${props.item.id}`);
      } else {
        await handleFailureAsync();
      }
    } catch (error) {
      SnackbarUtil.enqueueError(error);
    } finally {
      await props.refetchAsync();
    }
  }

  const handleCancelBidAsync = async () => {
    try {
      const success = await service.cancelBidAsync(props.item.id);
      if (success) {
        SnackbarUtil.enqueueMessage(`Bid cancelled`);
      } else {
        await handleFailureAsync();
      }
    } catch (error) {
      SnackbarUtil.enqueueError(error);
    } finally {
      await props.refetchAsync();
    }
  }

  const handleCancelSaleAsync = async (isGift: boolean = false) => {
    try {
      const success = await service.cancelOfferAsync(props.item.id);
      if (success) {
        const actName = isGift ? "Gift" : "Sale";
        SnackbarUtil.enqueueMessage(`${actName} cancelled`);
      } else {
        await handleFailureAsync();
      }
    } catch (error) {
      SnackbarUtil.enqueueError(error);
    } finally {
      await props.refetchAsync();
    }
  }

  const handleOpenDialog = (type: PunkDialogType) => {
    if (mounted.current) {
      setDialogState({
        ...dialogState,
        type: type,
        open: true
      })
    }
  }

  if (!hasOwner || !showButtonState.showAny) {
    return (
      <></>
    )
  }

  return (
    <>
      <Stack spacing={1} direction="row">
        {(showAll || showButtonState.showBuy) && (
          <ClickOnceButton
            variant="contained"
            color="primary"
            startIcon={<FontAwesomeSvgIcon icon={IconUtil.getBuyEventTypeIcon()} />}
            callbackFn={handleBuyAsync}>
            Buy
          </ClickOnceButton>
        )}
        {(showAll || showButtonState.showClaim) && (
          <ClickOnceButton
            variant="contained"
            color="primary"
            startIcon={<FontAwesomeSvgIcon icon={IconUtil.getClaimEventTypeIcon()} />}
            callbackFn={async () => { await handleBuyAsync(true) }}>
            Claim
          </ClickOnceButton>
        )}
        {(showAll || showButtonState.showBid) && (
          <Button
            variant="contained"
            color="primary"
            startIcon={<FontAwesomeSvgIcon icon={IconUtil.getPunkEventTypeIcon(PunkEventType.NewBid)} />}
            onClick={() => { handleOpenDialog(PunkDialogType.BID) }}>
            Bid
          </Button>
        )}
        {(showAll || showButtonState.showSell) && (
          <Button
            variant="contained"
            color="primary"
            startIcon={<FontAwesomeSvgIcon icon={IconUtil.getOfferEventTypeIcon()} />}
            onClick={() => { handleOpenDialog(PunkDialogType.OFFER) }}>
            Sell
          </Button>
        )}
        {(showAll || showButtonState.showAcceptBid) && (
          <ClickOnceButton
            variant="contained"
            color="primary"
            startIcon={<FontAwesomeSvgIcon icon={faCheck} />}
            callbackFn={handleAcceptBidAsync}>
            Accept bid
          </ClickOnceButton>
        )}
      </Stack>
      {(showAll || showButtonState.showCancelBid || showButtonState.showCancelSale || showButtonState.showCancelGift) && (
        <Stack spacing={1} direction="row" marginTop={1}>
          {(showAll || showButtonState.showCancelBid) && (
            <ClickOnceButton
              variant="contained"
              color="error"
              startIcon={<FontAwesomeSvgIcon icon={IconUtil.getPunkEventTypeIcon(PunkEventType.CancelBid)} />}
              callbackFn={handleCancelBidAsync}>
              Cancel bid
            </ClickOnceButton>
          )}
          {(showAll || showButtonState.showCancelSale) && (
            <ClickOnceButton
              variant="contained"
              color="error"
              startIcon={<FontAwesomeSvgIcon icon={IconUtil.getCancelSaleEventTypeIcon()} />}
              callbackFn={handleCancelSaleAsync}>
              Cancel sale
            </ClickOnceButton>
          )}
          {(showAll || showButtonState.showCancelGift) && (
            <ClickOnceButton
              variant="contained"
              color="error"
              startIcon={<FontAwesomeSvgIcon icon={IconUtil.getCancelSaleEventTypeIcon()} />}
              callbackFn={async () => { handleCancelSaleAsync(true) }}>
              Cancel gift
            </ClickOnceButton>
          )}
        </Stack>
      )}
      <PunkDialog item={props.item} state={dialogState} setState={setDialogState} refetchAsync={props.refetchAsync}></PunkDialog>
    </>
  )
}
