
import { faGem, faUserCircle } from '@fortawesome/free-regular-svg-icons';
import { faHandHoldingUsd, faTag } from '@fortawesome/free-solid-svg-icons';
import { Box, CardActionArea, Chip, styled } from '@mui/material';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { BigNumber } from 'bignumber.js';
import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { CommonConstants } from '../../../../common/constants';
import { CommonUtil } from '../../../../util/common.util';
import { TokenUtil } from '../../../../util/token.util';
import { Punk } from '../../../../util/types';
import { UrlUtil } from '../../../../util/url.util';
import { FontAwesomeSvgIcon } from '../../../common/components/fa-icon';
import { ExtendedBootstrapTooltip } from '../../../common/components/tooltip';

const CustomCardMedia = styled('div')(({ theme }) => (`
  position: relative;
  height: 320px;
  // animation: lightning 4s ease-out infinite;
  background-image: url('${CommonConstants.ITEM_BG_URL}');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
`));

const PunkImage = styled('div')(
  ({ theme }) => `
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  image-rendering: pixelated;
`,
);

interface Props {
  item: Punk
}

export const MarketplaceBrowseItem: React.FC<Props> = (props: Props) => {
  const truncateAddress = (address?: string) => {
    return CommonUtil.truncateStringInTheMiddle(address, 10, 5)
  }
  const formatViteTooltip = (bn: BigNumber) => {
    return TokenUtil.formatViteTooltip(bn);
  }
  const formatVite = (bn: BigNumber) => {
    return TokenUtil.formatVite(bn);
  }
  return (
    <Card>
      <CardActionArea component={RouterLink} to={`/punk/${props.item.id}`}>
        <CustomCardMedia>
          {props.item.owner && (
            <Box sx={{ position: "absolute", top: 5, left: 5 }}>
              <ExtendedBootstrapTooltip title={<span>{props.item.owner}</span>} placement="bottom" arrow>
                <Chip size="small" icon={<FontAwesomeSvgIcon icon={faUserCircle} sx={{ fontSize: "small", ml: 1 }} />} sx={{ fontSize: "0.7rem" }} label={truncateAddress(props.item.owner)} />
              </ExtendedBootstrapTooltip>
            </Box>
          )}
          <Box sx={{ position: "absolute", top: 5, right: 5 }}>
            <ExtendedBootstrapTooltip title={<span>Rarity rank</span>} placement="bottom" arrow>
              <Chip size="small" icon={<FontAwesomeSvgIcon icon={faGem} sx={{ fontSize: "small", ml: 1 }} />} sx={{ fontSize: "0.7rem" }} label={props.item.rank} />
            </ExtendedBootstrapTooltip>
          </Box>
          <Box sx={{ position: "absolute", bottom: 5, left: 5, width: "calc(100% - 10px)" }}>
            <Box>
              {props.item.bid && (
                <ExtendedBootstrapTooltip title={<span>Bid: {formatViteTooltip(props.item.bid.amount)}</span>} placement="bottom" arrow>
                  <Chip size="small" icon={<FontAwesomeSvgIcon icon={faHandHoldingUsd} sx={{ fontSize: "small", ml: 1 }} />} sx={{ fontSize: "0.7rem", mt: 1, mr: 1 }} label={formatVite(props.item.bid.amount)} />
                </ExtendedBootstrapTooltip>
              )}
              {props.item.offer && props.item.offer.minAmount.gt(0) && (
                <ExtendedBootstrapTooltip title={<span>Price: {formatViteTooltip(props.item.offer.minAmount)}</span>} placement="bottom" arrow>
                  <Chip size="small" icon={<FontAwesomeSvgIcon icon={faTag} sx={{ fontSize: "small", ml: 1 }} />} sx={{ fontSize: "0.7rem", mt: 1, float: "right" }} label={formatVite(props.item.offer.minAmount)} />
                </ExtendedBootstrapTooltip>
              )}
            </Box>
          </Box>
          <PunkImage sx={{ backgroundImage: `url('${UrlUtil.getPunkUrl(props.item.image)}')` }} />
        </CustomCardMedia>
        <CardContent sx={{ p: 0 }}>
          <Typography variant="h6" component="div" sx={{ p: 1 }}>
            {CommonConstants.PUNK_NAME} #{props.item.id}
          </Typography>
          <Box sx={{ height: "70px", overflow: "auto" }}>
            <Box sx={{ p: 1 }}>
              {props.item.attributes?.map((a: string) => (
                <Chip key={a} label={a} variant="outlined" sx={{ mb: 1, mr: 1 }} size="small" />
              ))}
            </Box>
          </Box>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}
