import { useQuery } from '@apollo/client';
import { faDiscord, faGithub, faTwitter } from '@fortawesome/free-brands-svg-icons';
import { Box, Button, Container, Grid, IconButton, Link, Paper, Stack, Typography } from '@mui/material';
import BigNumber from 'bignumber.js';
import React, { useEffect, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { CommonConstants } from '../../../../common/constants';
import { GET_MARKET_INFO_QUERY } from '../../../../queries';
import { GetMarketInfo } from '../../../../queries/__generated__/GetMarketInfo';
import { getPunkService } from '../../../../services/punk.service';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { MomentUtil } from '../../../../util/moment.util';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { TokenUtil } from '../../../../util/token.util';
import { DisplayBigNumber, DocumentState, GlobalEvent, MarketChangedEvent, PunkChangedEvent } from '../../../../util/types';
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { Countdown } from '../../../common/components/countdown';
import { FontAwesomeSvgIcon } from '../../../common/components/fa-icon';
import { LandingFaq } from '../faq';
import { MintsRemaining } from '../mints-remaining';
import { LandingRanking } from '../ranking';

const logger = getLogger();

export const LandingMain = () => {
  const moment = React.useMemo<MomentUtil>(() => {
    return new MomentUtil();
  }, []);
  const date = moment.getLocal("2021-12-21T17:00:00+0000");

  const mounted = React.useRef(false);
  const [mintPrice, setMintPrice] = useState<DisplayBigNumber>({
    number: new BigNumber("0"),
    displayNumber: "0"
  });
  const [marketStarted, setMarketStarted] = useState(false);
  const [canMint, setCanMint] = useState(false);
  const [mintsRemaining, setMintsRemaining] = useState<Maybe<number>>(undefined);
  const emitter = getEmitter();
  const punkService = getPunkService();

  const query = useQuery<GetMarketInfo>(GET_MARKET_INFO_QUERY, {
    fetchPolicy: 'network-only'
  });

  const refetch = query.refetch;

  useEffect(() => {
    mounted.current = true;
    return () => {
      mounted.current = false;
    };
  }, []);

  useEffect(() => {
    const handleEvent = (state: DocumentState) => {
      if (state === DocumentState.Active) {
        logger.info("Refreshing market info")();
        refetch();
      }
    }
    emitter.on(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    return () => {
      emitter.off(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    };
  }, [emitter, refetch]);

  useEffect(() => {
    const handleEvent = (e: MarketChangedEvent) => {
      logger.info(`eventType: ${e.eventType} amount: ${e.value.toFixed()} timestamp: ${e.timestamp.toFixed()}`)();
      refetch();
    }
    emitter.on(GlobalEvent.MarketChanged, handleEvent);
    return () => {
      emitter.off(GlobalEvent.MarketChanged, handleEvent);
    };
  }, [emitter, refetch]);

  useEffect(() => {
    const handleEvent = (e: PunkChangedEvent) => {
      logger.info(`eventType: ${e.eventType} punkId: ${e.punkId} amount: ${e.amount?.toFixed()} fromAddress: ${e.fromAddress} toAddress: ${e.toAddress} timestamp: ${e.timestamp.toFixed()}`)();
      refetch();
    }
    emitter.on(GlobalEvent.PunkChanged, handleEvent)
    return () => {
      emitter.off(GlobalEvent.PunkChanged, handleEvent);
    };
  }, [emitter, refetch]);

  useEffect(() => {
    const info = query.data?.marketInfo;
    if (info && mounted.current) {
      setMintPrice({
        number: new BigNumber(info.mintPrice),
        displayNumber: TokenUtil.formatVite(new BigNumber(info.mintPrice))
      });
      setMarketStarted(info.marketStarted);
      setCanMint(info.marketStarted && info.mintsRemaining > 0);
      setMintsRemaining(info.mintsRemaining);
    }
  }, [query.data]);

  const handleMintAsync = async () => {
    try {
      const result = await punkService.mintAsync();
      if (result) {
        SnackbarUtil.enqueueMessage("Mint success");
      } else {
        SnackbarUtil.enqueueError("Mint failed");
      }
    } catch (error) {
      logger.error(error)();
      SnackbarUtil.enqueueError(error);
    }
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 10 }}>
      <Grid container spacing={3} direction="row" justifyContent="center" alignItems="center" marginBottom={3}>
        <Grid item sm={12} md={6}>
          <Box display="flex" justifyContent="center" alignItems="center">
            <div>
              <Typography variant="h3" color="primary">
                {CommonConstants.APP_NAME}
              </Typography>
              <Typography variant="subtitle1" color="textSecondary" paragraph>
                The very first NFT collection powered by <Link underline="hover" href="https://www.vite.org/" target="_blank">Vite chain</Link>. Now is your chance to make Vite history and become owner of an isometric punk.
                Get yourself ready for the metaverse.
                {!marketStarted ? (
                  <>
                    &nbsp;Minting will open in:
                  </>

                ) : (
                  <>
                    &nbsp;Mint a punk for {mintPrice.displayNumber} {CommonConstants.CURRENCY_NAME} now!
                  </>
                )}
              </Typography>
              {!marketStarted ? (
                <Paper elevation={3} sx={{ p: 2, width: "60%", textAlign: "center", mb: 3 }}>
                  <Countdown date={date.toISOString()}></Countdown>
                </Paper>
              ) : (
                <MintsRemaining loading={query.loading} mintsRemaining={mintsRemaining}></MintsRemaining>
              )}
              <Stack spacing={2} direction="row">
                <ClickOnceButton variant="contained" color="primary" callbackFn={handleMintAsync} sx={{ minWidth: "100px" }} disabled={!canMint}>
                  Mint
                </ClickOnceButton>
                <Button variant="outlined" color="primary" component={RouterLink} to='/marketplace'>
                  Browse
                </Button>
              </Stack>
            </div>
          </Box>
        </Grid>
        <Grid item sm={12} md={6}>
          <Box display="flex" justifyContent="center" alignItems="center">
            {/* <LandingMainImage></LandingMainImage> */}
            <video width="100%" preload='' autoPlay muted playsInline loop style={{ opacity: "0.8" }}>
              <source src={CommonConstants.CDN_URL + 'videos/nye_640x360.mp4'} type="video/mp4"></source>
            </video>
          </Box>
        </Grid>
        <Grid item xs={12} marginTop={10}>
          <Box display="flex" justifyContent="center" alignItems="center">
            <Typography variant="subtitle1" color="textSecondary">
              Connect with us
            </Typography>
          </Box>
          <Box display="flex" justifyContent="center" alignItems="center">
            <Stack spacing={2} direction="row">
              <IconButton aria-label="Twitter" href={CommonConstants.TWITTER_URL} target="_blank">
                <FontAwesomeSvgIcon icon={faTwitter} />
              </IconButton>
              <IconButton aria-label="GitHub" href={CommonConstants.GITHUB_URL} target="_blank">
                <FontAwesomeSvgIcon icon={faGithub} />
              </IconButton>
              <IconButton aria-label="Discord" href={CommonConstants.DISCORD_URL} target="_blank">
                <FontAwesomeSvgIcon icon={faDiscord} />
              </IconButton>
            </Stack>
          </Box>
        </Grid>
        <Grid item xs={12} marginTop={10}>
          <LandingRanking></LandingRanking>
        </Grid>
        <Grid item xs={12} marginTop={10}>
          <LandingFaq></LandingFaq>
        </Grid>
        <Grid item xs={12} marginTop={2}>
          <Box display="flex" justifyContent="center" alignItems="center">
            <Box maxWidth={800}>
              <img width="100%" alt="main" src={CommonConstants.LANDING_IMAGE_URL}></img>
            </Box>
          </Box>
        </Grid>
        <Grid item xs={12} marginTop={1}>
          <Box display="flex" justifyContent="center" alignItems="center">
            <div>
              <Typography variant="h5" color="textSecondary" textAlign="center" paragraph>
                Disclaimer
              </Typography>
              <Typography variant="subtitle1" color="textSecondary" textAlign="center" paragraph>
                The intention of {CommonConstants.APP_NAME} is to showcase the capabilities of the Vite Blockchain. {CommonConstants.APP_NAME} is not affiliated with CryptoPunks and LarvaLabs.
                The value of a {CommonConstants.PUNK_NAME} is not related to the value of a CryptoPunk. {CommonConstants.APP_NAME} is a homage to the CryptoPunk project of LarvaLabs.
                With {CommonConstants.APP_NAME} we thank LarvaLabs for their pioneering and for creating the CryptoPunk project. The project that basically started the ever growing NFT movement.

                {marketStarted && mintPrice.number.gt(0) && (
                  <>
                    &nbsp;Why mint for {mintPrice.displayNumber} {CommonConstants.CURRENCY_NAME}?
                    The minting of {CommonConstants.APP_NAME} is set at {mintPrice.displayNumber} {CommonConstants.CURRENCY_NAME}.
                    This means that during the initial minting phase, you will be able to generate a random punk for {mintPrice.displayNumber} {CommonConstants.CURRENCY_NAME}.
                    When all 10,000 Punks are minted, you can get a Punk by using the marketplace.

                    That the minting is set at {mintPrice.displayNumber} {CommonConstants.CURRENCY_NAME} has a couple of reasons. As you know, keeping up, maintaining and further developing a system costs money.
                    We want to keep {CommonConstants.APP_NAME} running for years to come and we hope to integrate {CommonConstants.APP_NAME} with other NFT marketplaces in the near future.
                  </>
                )}
              </Typography>
            </div>
          </Box>
        </Grid>
      </Grid>
    </Container >
  );
}
