import { faBan, faDonate, faEraser, faGift, faHandHoldingUsd, faPowerOff, faQuestion, faTag, faTrophy } from '@fortawesome/free-solid-svg-icons';
import { BigNumber } from 'bignumber.js';
import { PunkEventType } from './types';

export abstract class IconUtil {
  static getBuyEventTypeIcon = () => {
    return faDonate;
  }
  static getClaimEventTypeIcon = () => {
    return faGift;
  }
  static getOfferEventTypeIcon = () => {
    return faTag;
  }
  static getCancelSaleEventTypeIcon = () => {
    return faPowerOff;
  }
  static getCancelGiftEventTypeIcon = () => {
    return faEraser;
  }
  static getPunkEventTypeIcon = (eventType: PunkEventType, amount?: Maybe<BigNumber>) => {
    switch (eventType) {
      case PunkEventType.Mint:
        return faTrophy;
      case PunkEventType.Offer:
        if (amount && amount.gt(0)) {
          return IconUtil.getOfferEventTypeIcon();
        }
        return IconUtil.getClaimEventTypeIcon();
      case PunkEventType.NewBid:
        return faHandHoldingUsd;;
      case PunkEventType.CancelBid:
        return faBan;
      case PunkEventType.Buy:
        if (amount && amount.gt(0)) {
          return IconUtil.getBuyEventTypeIcon();
        }
        return IconUtil.getClaimEventTypeIcon();
      case PunkEventType.CancelOffer:
        if (amount && amount.gt(0)) {
          return IconUtil.getCancelSaleEventTypeIcon();
        }
        return IconUtil.getCancelGiftEventTypeIcon();
      default:
        return faQuestion;
    }
  }
}