import { gql } from "@apollo/client";
import { ApolloContext } from "../clients/apollo.client";
import { MarketBid, MarketInfo, MarketOffer, PunkEvents } from "../util/types";
import { PUNK_EVENT_FIELDS } from "./punk";

export const MARKET_BID_FIELDS = gql`
  fragment MarketBidFields on MarketBid {
    itemIndex
    bidder
    amount
  }
`;

export const MARKET_OFFER_FIELDS = gql`
  fragment MarketOfferFields on MarketOffer {
    itemIndex
    seller
    minAmount
    onlySellTo
  }
`;

export const GET_MARKET_INFO_QUERY = gql`
  query GetMarketInfo {
    marketInfo @client {
      marketStarted
      mintsRemaining
      mintPrice
      actionPrice
    }
  }
`;

export const GET_MARKET_BID_QUERY = gql`
  ${MARKET_BID_FIELDS}
  query GetMarketBid($id: String!) {
    marketBid(id: $id) @client {
      ...MarketBidFields
    }
  }
`;

export const GET_MARKET_OFFER_QUERY = gql`
  ${MARKET_OFFER_FIELDS}
  query GetMarketOffer($id: String!) {
    marketOffer(id: $id) @client {
      ...MarketOfferFields
    }
  }
`;

export const GET_MARKET_EVENTS_QUERY = gql`
  ${PUNK_EVENT_FIELDS}
  query GetMarketEvents($limit: Int!, $offset: Int!, $height: String) {
    marketEvents(limit: $limit, offset: $offset, height: $height) @client {
      blockHeight
      events {
        ...PunkEventFields
      }
    }
  }
`;

export const MarketQueries = {
  async marketInfo(parent: any, params: any, context: ApolloContext): Promise<MarketInfo> {
    const result = await context.client.datasource.getMarketInfoAsync();
    return result;
  },
  async marketBid(parent: any, { id }: any, context: ApolloContext): Promise<Maybe<MarketBid>> {
    const result = await context.client.datasource.getMarketBidAsync(id);
    return result;
  },
  async marketOffer(parent: any, { id }: any, context: ApolloContext): Promise<Maybe<MarketOffer>> {
    const result = await context.client.datasource.getMarketOfferAsync(id);
    return result;
  },
  async marketEvents(parent: any, { limit, offset, height }: any, context: ApolloContext): Promise<PunkEvents> {
    const result = await context.client.datasource.getMarketEventsAsync(limit, offset, height);
    return result;
  }
}