import Big from 'big.js';
import { v4 as uuidv4 } from 'uuid';
import { DocumentState } from './types';

export abstract class CommonUtil {
  public static isString(value: any): boolean {
    return typeof value === 'string' || value instanceof String;
  }

  public static isNumber(value: any): boolean {
    return !isNaN(parseInt(value)) && isFinite(value);
  }

  public static isNullOrWhitespace(value?: Maybe<string>): boolean {
    if (!CommonUtil.isString(value)) {
      // console.log('Expected a string but got: ', value);
      return true;
    } else {
      return value === null || value === undefined || value.trim() === '';
    }
  }

  public static isNullOrDefault(value?: string, defaultValue?: string): boolean {
    return CommonUtil.isNullOrWhitespace(value) || value === defaultValue
  }

  public static timeout(ms: number): Promise<void> {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  public static truncateString = (str: Maybe<string>, maxLength: number) => {
    if (str) {
      if (maxLength < str.length) {
        return `${str.substring(0, maxLength)}...`
      }
    }
    return str
  }

  public static truncateStringInTheMiddle = (str: Maybe<string>, strPositionStart: number, strPositionEnd: number) => {
    if (str) {
      const minTruncatedLength = strPositionStart + strPositionEnd
      if (minTruncatedLength < str.length) {
        return `${str.substring(0, strPositionStart)}...${str.substring(str.length - strPositionEnd, str.length)}`
      }
    }
    return str
  }

  public static toJsonString(data: any): string {
    return JSON.stringify(data, null, 2);
  }

  public static toUint8Array(data: string, encoding: BufferEncoding = 'utf8'): Uint8Array {
    return new Uint8Array(Buffer.from(data, encoding));
  }

  public static getDocumentState = () => {
    if (document.visibilityState === 'hidden') {
      return DocumentState.Hidden;
    }
    return DocumentState.Active;
  };

  public static async imageUrlToBlob(url: string): Promise<Blob> {
    return (await fetch(url)).blob();
  }

  public static random(min: number, max: number) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  public static calculatePercentage(current: Maybe<string>, goal: Maybe<string>): number {
    try {
      if (current && goal) {
        return Math.floor(Big(current).div(Big(goal)).mul(100).toNumber());
      }
    } catch (error) {
    }
    return 0;
  }

  public static calculateMaxRows(total: number, rpp: number, page: number): number {
    return Math.min(Math.min(total - rpp * page, rpp), total);
  }

  public static uuid(): string {
    return uuidv4();
  }

  public static appendPath(_path: string, _appendix: string) {
    if (!_path || !_appendix || CommonUtil.isNullOrWhitespace(_path) || CommonUtil.isNullOrWhitespace(_appendix)) {
      return _path;
    }
    const delimiter = '/';
    const appendix = _appendix.startsWith(delimiter) ? _appendix.substring(1, _appendix.length) : _appendix;
    return _path.endsWith(delimiter) ? _path + appendix : _path + delimiter + appendix;
  }
}