import { Button, Dialog, DialogContent, DialogContentText, DialogTitle, FormControl, Input, InputLabel, Link, Typography } from '@mui/material';
import { Box } from '@mui/system';
import React, { useEffect, useState } from 'react';
import { CommonConfig } from '../../../../common/config';
import { getCommonContext } from '../../../../contexts/common.context';
import { CommonUtil } from '../../../../util/common.util';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { GlobalEvent } from '../../../../util/types';
import { getWalletManager, WalletConnectorFactory } from '../../../../wallet';
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { QrCode } from '../qrcode';

const logger = getLogger();

interface Props {
  open: boolean;
  setOpen: React.Dispatch<React.SetStateAction<boolean>>;
  onClose: () => void;
}

export const LoginDialog: React.FC<Props> = (props: Props) => {
  const [uri, setUri] = useState<string>("");
  const [error, setError] = useState(false);
  const [mnemonic, setMnemonic] = useState<Maybe<string>>(undefined);
  const { onClose, open, setOpen } = props;
  const context = getCommonContext();
  const walletManager = getWalletManager();
  const emitter = getEmitter();
  const connectorFactory = React.useMemo<WalletConnectorFactory>(() => {
    return new WalletConnectorFactory(walletManager);
  }, [walletManager]);
  const handleCreateSession = React.useCallback(async () => {
    try {
      const connector = await connectorFactory.createAsync(context.network);
      logger.info(connector.uri)();
      setUri(connector.uri);
      setError(false);
    } catch (error) {
      logger.error(error)();
      setError(true);
    }
  }, [connectorFactory, context.network]);

  useEffect(() => {
    const handleEvent = (open: boolean) => {
      setOpen(open)
    }
    emitter.on(GlobalEvent.ConnectWalletDialog, handleEvent)
    return () => {
      emitter.off(GlobalEvent.ConnectWalletDialog, handleEvent)
      setUri("");
      setError(false);
    };
  }, [emitter, setOpen]);

  useEffect(() => {
    if (open) {
      setMnemonic(undefined);
      handleCreateSession();
    }
  }, [open, handleCreateSession]);

  const handleClose = () => {
    onClose();
  };

  const handleMnemonicChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setMnemonic(event.target.value);
  };

  const handleLogin = () => {
    const wallet = walletManager.createWebWallet(CommonUtil.isNullOrWhitespace(mnemonic) ? process.env.REACT_APP_MNEMONIC ?? "" : mnemonic ?? "");
    if (!wallet) {
      SnackbarUtil.enqueueError('Invalid mnemonic');
    }
  };

  return (
    <Dialog onClose={handleClose} open={open} maxWidth="xs">
      <DialogTitle>Connect Wallet</DialogTitle>
      <DialogContent>
        <DialogContentText>
          Scan the QR code via Vite Wallet App
        </DialogContentText>
        <Box sx={{ mt: 2, textAlign: "center" }}>
          {CommonUtil.isNullOrWhitespace(uri) ? (
            <>
              {error ? (
                <>
                  <Typography gutterBottom>Error</Typography>
                  <ClickOnceButton variant="outlined" callbackFn={handleCreateSession}>Retry</ClickOnceButton>
                </>
              ) : (
                <>
                  Loading...
                </>
              )}
            </>
          ) : (
            <QrCode text={uri}></QrCode>
          )}
          <DialogContentText marginTop={1}>
            Don't have the Vite Wallet App? <Link underline="hover" href="https://vite.org/airdrop" target="_blank">Download here</Link>
          </DialogContentText>
        </Box>
        {CommonConfig.ENABLE_MNEMONIC_LOGIN && (
          <Box sx={{ mt: 2, textAlign: "center" }}>
            <FormControl fullWidth variant="standard" >
              <InputLabel htmlFor="mnemonic">Mnemonic</InputLabel>
              <Input
                id="mnemonic"
                value={mnemonic}
                onChange={handleMnemonicChange}
                type="text"
                autoComplete="new-password"
              />
            </FormControl>
            <Button variant="contained" sx={{ mt: 1 }} onClick={handleLogin}>Use test account</Button>
          </Box>
        )}
      </DialogContent>
    </Dialog>
  )
}
