import { gql } from "@apollo/client";
import { ApolloContext } from "../clients/apollo.client";
import { ViteQuotaResult } from "../util/types";

export const GET_QUOTA_QUERY = gql`
  query GetQuota($address: String!) {
    quota(address: $address) @client {
      currentQuota
      maxQuota
      stakeAmount
    }
  }
`;

export const GeneralQueries = {
  async quota(parent: any, { address }: any, context: ApolloContext): Promise<Maybe<ViteQuotaResult>> {
    const result = await context.client.datasource.getQuotaAsync(address);
    return result;
  },
}