import { useQuery } from '@apollo/client';
import SortIcon from '@mui/icons-material/Sort';
import { Autocomplete, FormControl, FormGroup, Grid, InputLabel, MenuItem, Select, SelectChangeEvent, Stack, TextField, ToggleButton, ToggleButtonGroup } from '@mui/material';
import React, { SyntheticEvent, useState } from 'react';
import { DefaultPunkFilterValues, PunkSortTypes } from '../../../../common/constants';
import { GET_PUNK_ATTRIBUTES_QUERY } from '../../../../queries';
import { GetPunkAttributes } from '../../../../queries/__generated__/GetPunkAttributes';
import { getEmitter } from '../../../../util/emitter';
import { PunkFilterValues } from '../../../../util/types';
import { OrderBy, PunkSortBy } from '../../../../__generated__/globalTypes';

export const MarketplaceBrowseItemsFilter: React.FC = () => {
  const [values, setValues] = useState<PunkFilterValues>(DefaultPunkFilterValues);
  const emitter = getEmitter();

  const attributesQuery = useQuery<GetPunkAttributes>(GET_PUNK_ATTRIBUTES_QUERY, {
    fetchPolicy: 'network-only'
  });

  const emitEvent = (oldValues: PunkFilterValues, newValues: PunkFilterValues) => {
    emitter.emitPunkFilterValuesChanged(oldValues, newValues);
  }

  const handleSortByChange = (event: SelectChangeEvent) => {
    const newValues: PunkFilterValues = { ...values, sortBy: event.target.value as PunkSortBy };
    setValues(newValues);
    emitEvent(values, newValues);
  };

  const handleSearchChange = (event: SyntheticEvent<Element, Event>, value: string[]) => {
    const newValues: PunkFilterValues = { ...values, search: value.join(',') };
    setValues(newValues);
    emitEvent(values, newValues);
  };

  const handleCheckedChange = (event: React.MouseEvent<HTMLElement, MouseEvent>, value: any) => {
    const newValues: PunkFilterValues = { ...values, orderBy: value };
    setValues(newValues);
    emitEvent(values, newValues);
  };

  return (
    <Grid container sx={{ mb: 2 }} spacing={3}>
      <Grid item md={3}></Grid>
      <Grid item xs={12} md={6}>
        <Autocomplete
          multiple
          freeSolo
          id="search-autocomplete"
          options={attributesQuery.data?.punkAttributes ?? []}
          getOptionLabel={(option) => option}
          size="small"
          fullWidth={true}
          onChange={handleSearchChange}
          filterSelectedOptions
          renderInput={(params) => (
            <TextField
              {...params}
              label="Search"
            />
          )}
        />
      </Grid>
      <Grid item xs={12} md={3}>
        <FormGroup>
          <Stack direction="row" justifyContent="end" spacing={2}>
            <FormControl size="small">
              <InputLabel id="sortby-select-label">Sort by</InputLabel>
              <Select
                labelId="sortby-select-label"
                id="sortby-select"
                value={values.sortBy}
                label="Sort by"
                onChange={handleSortByChange}
              >
                {PunkSortTypes.map(e => (
                  <MenuItem key={e.type} value={e.type}>{e.name}&nbsp;</MenuItem>
                ))}
              </Select>
            </FormControl>
            {/* <Stack direction="row" alignItems="center" >
              <Switch color="default" checked={values.orderByAsc} onChange={handleCheckedChange('orderByAsc')} />
              {values.orderByAsc ? (
                <ArrowUpward fontSize="small" />
              ) : (
                <ArrowDownward fontSize="small" />
              )}
            </Stack> */}
            <ToggleButtonGroup size="small" exclusive value={values.orderBy} onChange={handleCheckedChange}>
              <ToggleButton value={OrderBy.ASC}>
                <SortIcon sx={{ transform: "rotate(180deg)" }} />
              </ToggleButton>
              <ToggleButton value={OrderBy.DESC}>
                <SortIcon />
              </ToggleButton>
            </ToggleButtonGroup>
          </Stack>
        </FormGroup>
      </Grid>
    </Grid>
  )
}
