import { Button } from '@mui/material';
import confetti from 'canvas-confetti';
import React, { useCallback } from 'react';
import { getViteClient } from '../../../../clients/vite.client';
import { getWalletManager } from '../../../../wallet';

export const Test = () => {
  const walletManager = getWalletManager();
  const viteClient = getViteClient();

  const onClick = useCallback(() => {
    confetti({
      particleCount: 150,
      spread: 60
    });
    const wallet = walletManager.getWallet();
    if (wallet?.active) {
      viteClient.receiveAllOnroadTxAsync(wallet.active);
    }
  }, [viteClient, walletManager]);
  return (
    <>
      Test {process.env.REACT_APP_VERSION} {process.env.REACT_APP_EXPLORER_URL}
      <Button onClick={onClick}>Test</Button>
    </>
  );
}
