import { Card } from '@mui/material';
import { styled } from '@mui/material/styles';
import * as React from 'react';
import { CommonConstants } from '../../../../common/constants';
import { UrlUtil } from '../../../../util/url.util';

const CardContainer = styled(Card)(
  ({ theme }) => `
  width: 100%;
  height: 100vw; 
  background-color: rgba(255, 255, 255, 0.5);
`,
);

const ImageContainer = styled('div')(
  ({ theme }) => `
  position: relative;
  width: 100%;
  height: 100%;
`,
);

const Image = styled('div')(
  ({ theme }) => `
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url('${CommonConstants.ITEM_BG_URL}');
`,
);

const Img = styled('img')(
  ({ theme }) => `
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  image-rendering: pixelated;
`,
);

interface Props {
  image: string
  maxHeight?: string
}

export const PunkImage: React.FC<Props> = (props: Props) => {
  return (
    <CardContainer sx={{ maxHeight: props.maxHeight ?? "400px" }}>
      <ImageContainer>
        <Image>
          <Img sx={{ backgroundImage: `url('${UrlUtil.getPunkUrl(props.image)}')` }} />
        </Image>
      </ImageContainer>
    </CardContainer>
  );
}