import { useQuery } from '@apollo/client';
import React, { useCallback, useEffect, useState } from 'react';
import { GET_MARKET_EVENTS_QUERY } from '../../../../queries';
import { GetMarketEvents, GetMarketEventsVariables } from '../../../../queries/__generated__/GetMarketEvents';
import { getMarketService } from '../../../../services/market.service';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { TransformUtil } from '../../../../util/transform.util';
import { DocumentState, GlobalEvent, PunkChangedEvent, PunkEvent } from '../../../../util/types';
import { EventList } from '../../../common/components/event-list';

const logger = getLogger();

export const MarketplaceBrowseActivity: React.FC = (props: any) => {
  const limit = 8;
  const [isLoading, setIsLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [error, setError] = useState<any>(undefined);
  const [events, setEvents] = useState<PunkEvent[]>([]);
  const [newEvents, setNewEvents] = useState<PunkEvent[]>([]);
  const service = getMarketService();
  const emitter = getEmitter();

  const query = useQuery<GetMarketEvents, GetMarketEventsVariables>(GET_MARKET_EVENTS_QUERY, {
    variables: {
      offset: 0,
      limit: limit
    },
    fetchPolicy: 'network-only',
    onCompleted: (data) => {
      setEvents(data.marketEvents.events.map(e => TransformUtil.toPunkEvent(e)));
    }
  });

  useEffect(() => {
    setIsLoading(query.loading);
  }, [query.loading]);

  useEffect(() => {
    setError(query.error);
  }, [query.error]);

  const fetchMoreDataAsync = async () => {
    logger.info("fetchMoreDataAsync", events.length)();
    const blockHeight: Maybe<string> = query.data?.marketEvents.blockHeight.toString();
    setIsLoading(true);
    try {
      const result = await service.getEventsAsync(events.length, limit, blockHeight);
      setHasMore(result.data.marketEvents.events.length >= limit);
      if (result.data.marketEvents.events.length > 0) {
        setEvents([
          ...events,
          ...result.data.marketEvents.events.map(e => TransformUtil.toPunkEvent(e))
        ]);
      }
    } catch (error) {
      setError(error);
    }
    setIsLoading(false);
  };

  const refetch = query.refetch;

  const refresh = useCallback(() => {
    logger.info("Refreshing market activity")();
    refetch();
    setNewEvents([]);
  }, [refetch]);

  useEffect(() => {
    const handleEvent = (state: DocumentState) => {
      if (state === DocumentState.Active) {
        refresh();
      }
    }
    emitter.on(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    return () => {
      emitter.off(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    };
  }, [emitter, refresh]);

  const addNewEvent = useCallback((e: PunkChangedEvent) => {
    setNewEvents([
      TransformUtil.toPunkEvent(e),
      ...newEvents
    ]);
  }, [newEvents]);

  useEffect(() => {
    const handleEvent = (e: PunkChangedEvent) => {
      logger.info("newMarketEvent", e)();
      addNewEvent(e);
    }
    logger.info('emitter.on', GlobalEvent.PunkChanged)();
    emitter.on(GlobalEvent.PunkChanged, handleEvent);
    return () => {
      logger.info('emitter.off', GlobalEvent.PunkChanged)();
      emitter.off(GlobalEvent.PunkChanged, handleEvent);
    };
  }, [emitter, addNewEvent]);

  return (
    <EventList
      events={events}
      newEvents={newEvents}
      error={error}
      loading={isLoading}
      hasMore={hasMore}
      height={500}
      fetchMoreDataAsync={fetchMoreDataAsync}>
    </EventList>
  )
}
