import { abi as abiutils, accountBlock, utils, ViteAPI } from "@vite/vitejs";
import BigNumber from "bignumber.js";
import { CommonConstants } from "../common/constants";
import { CommonUtil } from "../util/common.util";
import { getEmitter, IGlobalEmitter } from "../util/emitter";
import { getLogger } from "../util/logger";
import { RetryUtil } from "../util/retry.util";
import { Task } from "../util/task";
import { Network, ViteQuotaResult, ViteVmLog, ViteVmLogEvent, VmLog } from "../util/types";
import { getWalletManager, IWalletConnector, SessionWalletAccount, WalletAccount, WalletConnectorFactory, WalletManager, WebWalletAccount } from "../wallet";
const { WS_RPC } = require('@vite/vitejs-ws');

const logger = getLogger();

const providerTimeout = 10000;
const providerOptions = { retryTimes: 10, retryInterval: 1000 };

export class ViteClient {
  private _network?: Network;
  private _retryCount: number;
  private _isConnected: boolean;
  private _connector?: IWalletConnector;
  private _provider?: any;
  private _client?: any;
  private readonly _emitter: IGlobalEmitter;
  private readonly _walletManager: WalletManager;
  private readonly _connectorFactory: WalletConnectorFactory;

  constructor() {
    this._retryCount = 0;
    this._isConnected = false;
    this._emitter = getEmitter();
    this._walletManager = getWalletManager();
    this._connectorFactory = new WalletConnectorFactory(this._walletManager);
  }

  get isConnected(): boolean {
    return this._isConnected;
  }

  get connector(): Maybe<IWalletConnector> {
    return this._connector;
  }

  initAsync = async (network: Network) => new Promise<void>((resolve, reject) => {
    logger.info("initAsync", network)();
    this._network = network;
    this._connector = this._connectorFactory.create(network);
    this._provider = new WS_RPC(network.rpcUrl, providerTimeout, providerOptions);
    let isResolved = false;
    // https://github.com/vitelabs/vite.js/blob/master/src/WS/index.js
    this._provider.on('close', () => {
      logger.info('Vite provider closed')();
      this._emitter.emitReInit();
    });
    this._provider.on('error', (err: any) => {
      logger.error('Vite provider error', this._retryCount)();
      if (isResolved) return;
      this._isConnected = false;
      if (this._retryCount >= providerOptions.retryTimes) {
        reject(err);
      } else {
        this._retryCount++;
      }
    });
    this._client = new ViteAPI(this._provider, () => {
      logger.info(`ViteAPI connected to ${this._provider.path}`)();
      isResolved = true;
      this._retryCount = 0;
      this._isConnected = true;
      resolve();
    });
  });

  dispose(): void {
    logger.info("Disposing ViteClient")();
    this._provider?.destroy();
    this._isConnected = false;
  }

  encodeParameter(type: string, param: string): string {
    return abiutils.encodeParameter(type, param);
  }

  encodeLogSignature(jsonFunction: any, methodName?: string): string {
    return abiutils.encodeLogSignature(jsonFunction, methodName);
  }

  async requestAsync(method: string, ...args: any[]): Promise<any> {
    if (this._isConnected && this._client) {
      const result = RetryUtil.promiseRetry(async () => {
        return this._client.request(method, ...args).catch(async (error: any) => {
          if (error?.message?.toLowerCase().includes("connection error")) {
            logger.error("CONNECTION ERROR -> reconnect", this._network)();
            this._emitter.emitReInit();
            await CommonUtil.timeout(3000);
            throw error;
          } else {
            throw error;
          }
        })
      });
      return result;
    } else {
      throw new Error('Vite client is not ready to make requests.');
    }
  }

  async getSnapshotChainHeightAsync(): Promise<BigNumber> {
    const result = await this.requestAsync('ledger_getSnapshotChainHeight');
    return new BigNumber(result);
  }

  async getAccountBlockHeightAsync(address: string): Promise<BigNumber> {
    const result = await this.requestAsync("ledger_getAccountInfoByAddress", address);
    return new BigNumber(result.blockCount);
  }

  async getBalanceAsync(address: string): Promise<BigNumber> {
    const result = await this.requestAsync("ledger_getAccountInfoByAddress", address);
    return new BigNumber(result.balanceInfoMap[CommonConstants.VITE_TOKEN_ID].balance);
  }

  async getQuotaAsync(address: string): Promise<ViteQuotaResult> {
    const result = await this.requestAsync("contract_getQuotaByAccount", address);
    return {
      currentQuota: new BigNumber(result.currentQuota),
      maxQuota: new BigNumber(result.maxQuota),
      stakeAmount: new BigNumber(result.stakeAmount)
    }
  }

  async sendAsync(account: WalletAccount, toAddress: string, amount: string, tokenId: any = CommonConstants.VITE_TOKEN_ID): Promise<any> {
    let block = accountBlock
      .createAccountBlock("send", {
        address: account.address,
        toAddress: toAddress,
        tokenId,
        amount
      });

    return this.sendAccountBlockAsync(account, block);
  }

  async stakeForQuotaAsync(account: WalletAccount, beneficiaryAddress: string, amount: string): Promise<any> {
    let block = accountBlock
      .createAccountBlock("stakeForQuota", {
        address: account.address,
        beneficiaryAddress: beneficiaryAddress,
        amount
      });

    return this.sendAccountBlockAsync(account, block);
  }

  async callContractAsync(account: WalletAccount, methodName: string, abi: any, params: any, toAddress: string, amount: string = '0', tokenId: any = CommonConstants.VITE_TOKEN_ID): Promise<any> {
    let block = accountBlock
      .createAccountBlock("callContract", {
        address: account.address,
        abi,
        methodName,
        tokenId,
        amount,
        toAddress,
        params,
      });

    return this.sendAccountBlockAsync(account, block);
  }

  async createContractAsync(account: WalletAccount, abi: any, binary: any): Promise<any> {
    let responseLatency = 1;
    let quotaMultiplier = 10;
    let randomDegree = 1;
    let block = accountBlock
      .createAccountBlock("createContract", {
        address: account.address,
        abi,
        code: binary,
        responseLatency,
        params: [],
        quotaMultiplier,
        randomDegree
      });

    return this.sendAccountBlockAsync(account, block);
  }

  async callOffChainMethodAsync(contractAddress: string, abi: any, offchaincode: string, params: any): Promise<any> {
    let data = abiutils.encodeFunctionCall(abi, params);
    let dataBase64 = Buffer.from(data, "hex").toString("base64");
    let result = await this.requestAsync("contract_callOffChainMethod", {
      selfAddr: contractAddress,
      offChainCode: offchaincode,
      data: dataBase64,
    });
    if (result) {
      let resultBytes = Buffer.from(result, "base64").toString("hex");
      let outputs = [];
      for (let i = 0; i < abi.outputs.length; i++) {
        outputs.push(abi.outputs[i].type);
      }
      let offchainDecodeResult = abiutils.decodeParameters(outputs, resultBytes);
      let resultList = [];
      if (offchainDecodeResult) {
        for (let i = 0; i < abi.outputs.length; i++) {
          if (abi.outputs[i].name) {
            resultList.push({
              name: abi.outputs[i].name,
              value: offchainDecodeResult[i],
            });
          } else {
            resultList.push({
              name: "",
              value: offchainDecodeResult[i],
            });
          }
        }
      }
      return resultList;
    }
    return "";
  }

  decodeVmLog(vmLog: ViteVmLog, abi: any): Maybe<VmLog> {
    let topics = vmLog.topics;
    for (let j = 0; j < abi.length; j++) {
      let abiItem = abi[j];
      if (abiutils.encodeLogSignature(abiItem) === topics[0]) {
        let result = this.decodeVmLogByAbiItem(vmLog, abiItem);
        if (result) {
          return result;
        }
      }
    }
    return undefined;
  }

  decodeVmLogByAbiItem(vmLog: ViteVmLog, abiItem: any): Maybe<VmLog> {
    if (!vmLog || !vmLog.data || !vmLog.topics) {
      return undefined;
    }
    let topics = vmLog.topics;
    let log: VmLog = {
      topic: topics[0],
      args: abiutils.decodeLog(
        abiItem.inputs,
        utils._Buffer.from(vmLog.data, "base64").toString("hex"),
        topics.slice(1)
      ),
      event: abiItem.name,
    };
    return log;
  }

  async getEventsByBlockAsync(address: string, fromHeight: BigNumber, toHeight: BigNumber, topics?: string[][]): Promise<ViteVmLogEvent[]> {
    if (fromHeight.gt(toHeight)) {
      logger.info(`getEventsByBlockAsync ${fromHeight.toFixed()} > ${toHeight.toFixed()}`)();
      return [];
    }
    const payload = {
      addressHeightRange: {
        [address]: {
          fromHeight: fromHeight.toFixed(),
          toHeight: toHeight.toFixed(),
        },
      },
      topics: topics
    };
    try {
      const result = await this.requestAsync("ledger_getVmLogsByFilter", payload);
      return result;
    } catch (error) {
      const midHeight = fromHeight.plus(toHeight).div(2);
      logger.info(`Divide and conquer block fromHeight: ${fromHeight.toFixed()} toHeight: ${toHeight.toFixed()} midHeight: ${midHeight.toFixed()}`)();
      //const arr1 = await this.getEventsByBlockAsync(address, fromHeight, midHeight, topics);
      const arr2 = await this.getEventsByBlockAsync(address, midHeight.plus(1), toHeight, topics);
      //return [...arr1, ...arr2];
      return arr2;
    }
  }

  async createAddressListenerAsync(address: string): Promise<any> {
    const payload = {
      addressHeightRange: {
        [address]: {
          fromHeight: "0",
          toHeight: "0",
        },
      },
    };
    const result = await this._client?.subscribe("createVmlogSubscription", payload);
    return result;
  }

  removeListener(event: any): void {
    logger.info('removeListener')();
    this._client?.unsubscribe(event);
  }

  async waitForNextVmLogEventAsync(listener: any, abi: any): Promise<Maybe<VmLog>> {
    logger.info('waitForNextVmLogEvent')();
    return new Promise(async (resolve) => {
      // listener = EventEmitter
      // https://github.com/vitelabs/vite.js/blob/14b87a6ddc68fe5836816f949f85d6161e6cfed8/src/viteAPI/eventEmitter.ts
      listener.on(async (results: any[]) => {
        if (results.length !== 1) {
          console.log(results)
          throw new Error('Unexpected results');
        }
        const result = results[results.length - 1];
        // console.log(result);
        const vmLog = this.decodeVmLog(result.vmlog, abi);
        logger.info("vmLog", vmLog)();
        // https://github.com/vitelabs/vite.js/blob/14b87a6ddc68fe5836816f949f85d6161e6cfed8/src/viteAPI/provider.ts
        // Don't unsubscribe, listener should be reusable!
        // provider.unsubscribe(listener);
        resolve(vmLog);
      });
    });
  }

  async waitForAccountBlockAsync(address: string, height: string): Promise<any> {
    return new Promise(async (resolve, reject) => {
      let result: any = undefined;
      let error: any = undefined;
      let timestamp = Date.now();
      const task = new Task(async () => {
        try {
          if (Date.now() - timestamp >= providerTimeout) {
            throw new Error("Task timed out")
          }

          let blockByHeight = await this.requestAsync(
            'ledger_getAccountBlockByHeight',
            address,
            height
          );

          if (!blockByHeight) {
            return true;
          }

          let receiveBlockHash = blockByHeight.receiveBlockHash;
          if (!receiveBlockHash) {
            return true;
          }

          let blockByHash = await this.requestAsync('ledger_getAccountBlockByHash', receiveBlockHash);
          if (!blockByHash) {
            return true;
          }

          result = {
            ...this.getAccountBlockStatus(blockByHash),
            accountBlock: blockByHash
          }

          return false;
        } catch (err) {
          error = err;
          return false;
        }
      }, 500);
      task.start(() => {
        if (error) {
          reject(error);
        } else {
          resolve(result);
        }
      });
    });
  }

  async receiveTransactionAsync(account: WalletAccount, block?: any): Promise<any> {
    if (!block) {
      // get the first unreceived block
      let blocks = await this.requestAsync(
        "ledger_getUnreceivedBlocksByAddress",
        account.address,
        0,
        1
      );
      if (blocks && blocks.length > 0) {
        block = blocks[0];
      }
    }

    if (!block) {
      logger.info(`No Unreceived Blocks: ${account.address}`)();
      return;
    }

    let ab = accountBlock
      .createAccountBlock("receive", {
        address: account.address,
        sendBlockHash: block.hash,
      });

    return this.sendAccountBlockAsync(account, ab);
  }

  async receiveAllOnroadTxAsync(account: WalletAccount): Promise<void> {
    const RECEIVE_PER_ROUND = 10;
    let blocks = await this.requestAsync(
      "ledger_getUnreceivedBlocksByAddress",
      account.address,
      0,
      RECEIVE_PER_ROUND
    );

    for (let i = 0; i < blocks.length; i++) {
      let block = blocks[i];
      await this.receiveTransactionAsync(account, block);
    }
    if (blocks.length >= RECEIVE_PER_ROUND) {
      await this.receiveAllOnroadTxAsync(account);
    }
  }

  private async sendAccountBlockAsync(account: WalletAccount, block: any): Promise<any> {
    if (account instanceof WebWalletAccount) {
      block = block.setProvider(this._client).setPrivateKey(account.privateKey);
      await block.autoSetPreviousAccountBlock();
      const result = await block.sign().send();
      return result;
    } else if (account instanceof SessionWalletAccount) {
      if (this.connector) {
        this._emitter.emitConfirmTransactionDialog(true);
        try {
          const result = await this.connector.sendTransactionAsync({
            block: block.accountBlock
          });
          this._emitter.emitConfirmTransactionDialog(false);
          return result;
        } catch (error) {
          this._emitter.emitConfirmTransactionDialog(false);
          throw error
        }
      } else {
        throw new Error("Connector is not defined");
      }
    } else {
      throw new Error("Account not supported");
    }
  }

  private getAccountBlockStatus(accountBlock: any) {
    let status = this.resolveAccountBlockData(accountBlock);
    let statusTxt = '';
    switch (status) {
      case 0:
        statusTxt = '0, Execution succeed';
        break;
      case 1:
        statusTxt = '1, Execution reverted';
        break;
      case 2:
        statusTxt = '2, Max call depth exceeded';
        break;
    }
    return {
      status,
      statusTxt
    };
  }

  private resolveAccountBlockData(accountBlock: any) {
    if (
      (accountBlock.blockType !== 4 && accountBlock.blockType !== 5) ||
      !accountBlock.data
    ) {
      return 0;
    }
    let bytes = utils._Buffer.from(accountBlock.data, 'base64');

    if (bytes.length !== 33) {
      return 0;
    }
    return bytes[32];
  }
}

const client = new ViteClient();

export const getViteClient = () => {
  return client;
}